				description_md: "Specifies a version starting from which a browser is considered modern.\nA browser can be any one of the following: `msie`,\n`gecko` (browsers based on Mozilla),\n`opera`, `safari`,\nor `konqueror`.\n\nVersions can be specified in the following formats: X, X.X, X.X.X, or X.X.X.X.\nThe maximum values for each of the format are\n4000, 4000.99, 4000.99.99, and 4000.99.99.99, respectively.\n\nThe special value `unlisted` specifies to consider\na browser as modern if it was not listed by the\n`modern_browser` and [`ancient_browser`](https://nginx.org/en/docs/http/ngx_http_browser_module.html#ancient_browser)\ndirectives.\nOtherwise such a browser is considered ancient.\nIf a request does not provide the \"User-Agent\" field\nin the header, the browser is treated as not being listed.",
				description_html: "<p>Specifies a version starting from which a browser is considered modern.\nA browser can be any one of the following: <code>msie</code>,\n<code>gecko</code> (browsers based on Mozilla),\n<code>opera</code>, <code>safari</code>,\nor <code>konqueror</code>.</p>\n\n<p>Versions can be specified in the following formats: X, X.X, X.X.X, or X.X.X.X.\nThe maximum values for each of the format are\n4000, 4000.99, 4000.99.99, and 4000.99.99.99, respectively.</p>\n\n<p>The special value <code>unlisted</code> specifies to consider\na browser as modern if it was not listed by the\n<code>modern_browser</code> and <a href=\"https://nginx.org/en/docs/http/ngx_http_browser_module.html#ancient_browser\" target=\"_blank\"><code>ancient_browser</code></a>\ndirectives.\nOtherwise such a browser is considered ancient.\nIf a request does not provide the &ldquo;User-Agent&rdquo; field\nin the header, the browser is treated as not being listed.</p>\n"
