				description_html: "<p>Defines a replacement for the specified location.\nFor example, with the following configuration</p>\n\n<pre><code>location /i/ {\n    alias /data/w3/images/;\n}\n</code></pre>\n\n<p>on request of\n“<code>/i/top.gif</code>”, the file\n<code>/data/w3/images/top.gif</code> will be sent.</p>\n\n<p>The <em><code>path</code></em> value can contain variables,\nexcept <code>$document_root</code> and <code>$realpath_root</code>.</p>\n\n<p>If <code>alias</code> is used inside a location defined\nwith a regular expression then such regular expression should\ncontain captures and <code>alias</code> should refer to\nthese captures (0.7.40), for example:</p>\n\n<pre><code>location ~ ^/users/(.+\\.(?:gif|jpe?g|png))$ {\n    alias /data/w3/images/$1;\n}\n</code></pre>\n\n<p>When location matches the last part of the directive’s value:</p>\n\n<pre><code>location /images/ {\n    alias /data/w3/images/;\n}\n</code></pre>\n\n<p>it is better to use the\n<a href=\"https://nginx.org/en/docs/http/ngx_http_core_module.html#root\" target=\"_blank\"><code>root</code></a>\ndirective instead:</p>\n\n<pre><code>location /images/ {\n    root /data/w3;\n}\n</code></pre>\n"
