				description_md: "Limits the rate of response transmission to a client.\nThe *`rate`* is specified in bytes per second.\nThe zero value disables rate limiting.\n\nThe limit is set per a request, and so if a client simultaneously opens\ntwo connections, the overall rate will be twice as much\nas the specified limit.\n\nParameter value can contain variables (1.17.0).\nIt may be useful in cases where rate should be limited\ndepending on a certain condition:\n```\nmap $slow $rate {\n    1     4k;\n    2     8k;\n}\n\nlimit_rate $rate;\n```\n\nRate limit can also be set in the\n[`$limit_rate`](https://nginx.org/en/docs/http/ngx_http_core_module.html#var_limit_rate) variable,\nhowever, since version 1.17.0, this method is not recommended:\n```\nserver {\n\n    if ($slow) {\n        set $limit_rate 4k;\n    }\n\n    ...\n}\n```\n\nRate limit can also be set in the\n\"X-Accel-Limit-Rate\" header field of a proxied server response.\nThis capability can be disabled using the\n[`proxy_ignore_headers`](https://nginx.org/en/docs/http/ngx_http_proxy_module.html#proxy_ignore_headers),\n[`fastcgi_ignore_headers`](https://nginx.org/en/docs/http/ngx_http_fastcgi_module.html#fastcgi_ignore_headers),\n[`uwsgi_ignore_headers`](https://nginx.org/en/docs/http/ngx_http_uwsgi_module.html#uwsgi_ignore_headers),\nand\n[`scgi_ignore_headers`](https://nginx.org/en/docs/http/ngx_http_scgi_module.html#scgi_ignore_headers)\ndirectives.",
				description_html: "<p>Limits the rate of response transmission to a client.\nThe <em><code>rate</code></em> is specified in bytes per second.\nThe zero value disables rate limiting.</p>\n\n<p>The limit is set per a request, and so if a client simultaneously opens\ntwo connections, the overall rate will be twice as much\nas the specified limit.</p>\n\n<p>Parameter value can contain variables (1.17.0).\nIt may be useful in cases where rate should be limited\ndepending on a certain condition:</p>\n\n<pre><code>map $slow $rate {\n    1     4k;\n    2     8k;\n}\n\nlimit_rate $rate;\n</code></pre>\n\n<p>Rate limit can also be set in the\n<a href=\"https://nginx.org/en/docs/http/ngx_http_core_module.html#var_limit_rate\" target=\"_blank\"><code>$limit_rate</code></a> variable,\nhowever, since version 1.17.0, this method is not recommended:</p>\n\n<pre><code>server {\n\n    if ($slow) {\n        set $limit_rate 4k;\n    }\n\n    ...\n}\n</code></pre>\n\n<p>Rate limit can also be set in the\n&ldquo;X-Accel-Limit-Rate&rdquo; header field of a proxied server response.\nThis capability can be disabled using the\n<a href=\"https://nginx.org/en/docs/http/ngx_http_proxy_module.html#proxy_ignore_headers\" target=\"_blank\"><code>proxy_ignore_headers</code></a>,\n<a href=\"https://nginx.org/en/docs/http/ngx_http_fastcgi_module.html#fastcgi_ignore_headers\" target=\"_blank\"><code>fastcgi_ignore_headers</code></a>,\n<a href=\"https://nginx.org/en/docs/http/ngx_http_uwsgi_module.html#uwsgi_ignore_headers\" target=\"_blank\"><code>uwsgi_ignore_headers</code></a>,\nand\n<a href=\"https://nginx.org/en/docs/http/ngx_http_scgi_module.html#scgi_ignore_headers\" target=\"_blank\"><code>scgi_ignore_headers</code></a>\ndirectives.</p>\n"
