				description_md: "Enables or disables compression of two or more adjacent slashes\nin a URI into a single slash.\n\nNote that compression is essential for the correct matching of prefix string\nand regular expression locations.\nWithout it, the “`//scripts/one.php`” request would not match\n```\nlocation /scripts/ {\n    ...\n}\n```\nand might be processed as a static file.\nSo it gets converted to “`/scripts/one.php`”.\n\nTurning the compression `off` can become necessary if a URI\ncontains base64-encoded names, since base64 uses the “`/`”\ncharacter internally.\nHowever, for security considerations, it is better to avoid turning\nthe compression off.\n\nIf the directive is specified on the [`server`](https://nginx.org/en/docs/http/ngx_http_core_module.html#server) level,\nthe value from the default server can be used.\nDetails are provided in the\n“[Virtual server selection](https://nginx.org/en/docs/http/server_names.html#virtual_server_selection)” section.",
				description_html: "<p>Enables or disables compression of two or more adjacent slashes\nin a URI into a single slash.</p>\n\n<p>Note that compression is essential for the correct matching of prefix string\nand regular expression locations.\nWithout it, the “<code>//scripts/one.php</code>” request would not match</p>\n\n<pre><code>location /scripts/ {\n    ...\n}\n</code></pre>\n\n<p>and might be processed as a static file.\nSo it gets converted to “<code>/scripts/one.php</code>”.</p>\n\n<p>Turning the compression <code>off</code> can become necessary if a URI\ncontains base64-encoded names, since base64 uses the “<code>/</code>”\ncharacter internally.\nHowever, for security considerations, it is better to avoid turning\nthe compression off.</p>\n\n<p>If the directive is specified on the <a href=\"https://nginx.org/en/docs/http/ngx_http_core_module.html#server\" target=\"_blank\"><code>server</code></a> level,\nthe value from the default server can be used.\nDetails are provided in the\n“<a href=\"https://nginx.org/en/docs/http/server_names.html#virtual_server_selection\" target=\"_blank\">Virtual server selection</a>” section.</p>\n"
