				isBlock: false,
				description_md: "Configures a cache that can store:\n- open file descriptors, their sizes and modification times;\n- information on existence of directories;\n- file lookup errors, such as “file not found”, “no read permission”,\n    and so on.\n    > Caching of errors should be enabled separately by the\n    > [`open_file_cache_errors`](https://nginx.org/en/docs/http/ngx_http_core_module.html#open_file_cache_errors)\n    > directive.\n\nThe directive has the following parameters:\n- `max`\n\n    sets the maximum number of elements in the cache;\n    on cache overflow the least recently used (LRU) elements are removed;\n- `inactive`\n\n    defines a time after which an element is removed from the cache\n    if it has not been accessed during this time;\n    by default, it is 60 seconds;\n- `off`\n\n    disables the cache.\n\nExample:\n```\nopen_file_cache          max=1000 inactive=20s;\nopen_file_cache_valid    30s;\nopen_file_cache_min_uses 2;\nopen_file_cache_errors   on;\n```",
				description_html: "<p>Configures a cache that can store:</p>\n\n<ul>\n<li>open file descriptors, their sizes and modification times;</li>\n<li>information on existence of directories;</li>\n<li>file lookup errors, such as “file not found”, “no read permission”,\nand so on.\n&gt; Caching of errors should be enabled separately by the\n&gt; <a href=\"https://nginx.org/en/docs/http/ngx_http_core_module.html#open_file_cache_errors\" target=\"_blank\"><code>open_file_cache_errors</code></a>\n&gt; directive.</li>\n</ul>\n\n<p>The directive has the following parameters:</p>\n\n<ul>\n<li><p><code>max</code></p>\n\n<p>sets the maximum number of elements in the cache;\non cache overflow the least recently used (LRU) elements are removed;</p></li>\n\n<li><p><code>inactive</code></p>\n\n<p>defines a time after which an element is removed from the cache\nif it has not been accessed during this time;\nby default, it is 60 seconds;</p></li>\n\n<li><p><code>off</code></p>\n\n<p>disables the cache.</p></li>\n</ul>\n\n<p>Example:</p>\n\n<pre><code>open_file_cache          max=1000 inactive=20s;\nopen_file_cache_valid    30s;\nopen_file_cache_min_uses 2;\nopen_file_cache_errors   on;\n</code></pre>\n"
