				isBlock: true,
				description_md: "Sets configuration for a virtual server.\nThere is no clear separation between IP-based (based on the IP address)\nand name-based (based on the \"Host\" request header field)\nvirtual servers.\nInstead, the [`listen`](https://nginx.org/en/docs/http/ngx_http_core_module.html#listen) directives describe all\naddresses and ports that should accept connections for the server, and the\n[`server_name`](https://nginx.org/en/docs/http/ngx_http_core_module.html#server_name) directive lists all server names.\nExample configurations are provided in the\n“[How nginx processes a request](https://nginx.org/en/docs/http/request_processing.html)” document.",
				description_html: "<p>Sets configuration for a virtual server.\nThere is no clear separation between IP-based (based on the IP address)\nand name-based (based on the &ldquo;Host&rdquo; request header field)\nvirtual servers.\nInstead, the <a href=\"https://nginx.org/en/docs/http/ngx_http_core_module.html#listen\" target=\"_blank\"><code>listen</code></a> directives describe all\naddresses and ports that should accept connections for the server, and the\n<a href=\"https://nginx.org/en/docs/http/ngx_http_core_module.html#server_name\" target=\"_blank\"><code>server_name</code></a> directive lists all server names.\nExample configurations are provided in the\n“<a href=\"https://nginx.org/en/docs/http/request_processing.html\" target=\"_blank\">How nginx processes a request</a>” document.</p>\n"
