				syntax_html: [
					"<p><em><code>file</code></em> &hellip; <em><code>uri</code></em></p>\n",
					"<p><em><code>file</code></em> &hellip; =<em><code>code</code></em></p>\n"
				],
				isBlock: false,
				description_md: "Checks the existence of files in the specified order and uses\nthe first found file for request processing; the processing\nis performed in the current context.\nThe path to a file is constructed from the\n*`file`* parameter\naccording to the\n[`root`](https://nginx.org/en/docs/http/ngx_http_core_module.html#root) and [`alias`](https://nginx.org/en/docs/http/ngx_http_core_module.html#alias) directives.\nIt is possible to check directory’s existence by specifying\na slash at the end of a name, e.g. “`$uri/`”.\nIf none of the files were found, an internal redirect to the\n*`uri`* specified in the last parameter is made.\nFor example:\n```\nlocation /images/ {\n    try_files $uri /images/default.gif;\n}\n\nlocation = /images/default.gif {\n    expires 30s;\n}\n```\nThe last parameter can also point to a named location,\nas shown in examples below.\nStarting from version 0.7.51, the last parameter can also be a\n*`code`*:\n```\nlocation / {\n    try_files $uri $uri/index.html $uri.html =404;\n}\n```\n\nExample in proxying Mongrel:\n```\nlocation / {\n    try_files /system/maintenance.html\n              $uri $uri/index.html $uri.html\n              @mongrel;\n}\n\nlocation @mongrel {\n    proxy_pass http://mongrel;\n}\n```\n\nExample for Drupal/FastCGI:\n```\nlocation / {\n    try_files $uri $uri/ @drupal;\n}\n\nlocation ~ \\.php$ {\n    try_files $uri @drupal;\n\n    fastcgi_pass ...;\n\n    fastcgi_param SCRIPT_FILENAME /path/to$fastcgi_script_name;\n    fastcgi_param SCRIPT_NAME     $fastcgi_script_name;\n    fastcgi_param QUERY_STRING    $args;\n\n    ... other fastcgi_param's\n}\n\nlocation @drupal {\n    fastcgi_pass ...;\n\n    fastcgi_param SCRIPT_FILENAME /path/to/index.php;\n    fastcgi_param SCRIPT_NAME     /index.php;\n    fastcgi_param QUERY_STRING    q=$uri&$args;\n\n    ... other fastcgi_param's\n}\n```\nIn the following example,\n```\nlocation / {\n    try_files $uri $uri/ @drupal;\n}\n```\nthe `try_files` directive is equivalent to\n```\nlocation / {\n    error_page 404 = @drupal;\n    log_not_found off;\n}\n```\nAnd here,\n```\nlocation ~ \\.php$ {\n    try_files $uri @drupal;\n\n    fastcgi_pass ...;\n\n    fastcgi_param SCRIPT_FILENAME /path/to$fastcgi_script_name;\n\n    ...\n}\n```\n`try_files` checks the existence of the PHP file\nbefore passing the request to the FastCGI server.\n\nExample for Wordpress and Joomla:\n```\nlocation / {\n    try_files $uri $uri/ @wordpress;\n}\n\nlocation ~ \\.php$ {\n    try_files $uri @wordpress;\n\n    fastcgi_pass ...;\n\n    fastcgi_param SCRIPT_FILENAME /path/to$fastcgi_script_name;\n    ... other fastcgi_param's\n}\n\nlocation @wordpress {\n    fastcgi_pass ...;\n\n    fastcgi_param SCRIPT_FILENAME /path/to/index.php;\n    ... other fastcgi_param's\n}\n```",
				description_html: "<p>Checks the existence of files in the specified order and uses\nthe first found file for request processing; the processing\nis performed in the current context.\nThe path to a file is constructed from the\n<em><code>file</code></em> parameter\naccording to the\n<a href=\"https://nginx.org/en/docs/http/ngx_http_core_module.html#root\" target=\"_blank\"><code>root</code></a> and <a href=\"https://nginx.org/en/docs/http/ngx_http_core_module.html#alias\" target=\"_blank\"><code>alias</code></a> directives.\nIt is possible to check directory’s existence by specifying\na slash at the end of a name, e.g. “<code>$uri/</code>”.\nIf none of the files were found, an internal redirect to the\n<em><code>uri</code></em> specified in the last parameter is made.\nFor example:</p>\n\n<pre><code>location /images/ {\n    try_files $uri /images/default.gif;\n}\n\nlocation = /images/default.gif {\n    expires 30s;\n}\n</code></pre>\n\n<p>The last parameter can also point to a named location,\nas shown in examples below.\nStarting from version 0.7.51, the last parameter can also be a\n<em><code>code</code></em>:</p>\n\n<pre><code>location / {\n    try_files $uri $uri/index.html $uri.html =404;\n}\n</code></pre>\n\n<p>Example in proxying Mongrel:</p>\n\n<pre><code>location / {\n    try_files /system/maintenance.html\n              $uri $uri/index.html $uri.html\n              @mongrel;\n}\n\nlocation @mongrel {\n    proxy_pass http://mongrel;\n}\n</code></pre>\n\n<p>Example for Drupal/FastCGI:</p>\n\n<pre><code>location / {\n    try_files $uri $uri/ @drupal;\n}\n\nlocation ~ \\.php$ {\n    try_files $uri @drupal;\n\n    fastcgi_pass ...;\n\n    fastcgi_param SCRIPT_FILENAME /path/to$fastcgi_script_name;\n    fastcgi_param SCRIPT_NAME     $fastcgi_script_name;\n    fastcgi_param QUERY_STRING    $args;\n\n    ... other fastcgi_param's\n}\n\nlocation @drupal {\n    fastcgi_pass ...;\n\n    fastcgi_param SCRIPT_FILENAME /path/to/index.php;\n    fastcgi_param SCRIPT_NAME     /index.php;\n    fastcgi_param QUERY_STRING    q=$uri&amp;$args;\n\n    ... other fastcgi_param's\n}\n</code></pre>\n\n<p>In the following example,</p>\n\n<pre><code>location / {\n    try_files $uri $uri/ @drupal;\n}\n</code></pre>\n\n<p>the <code>try_files</code> directive is equivalent to</p>\n\n<pre><code>location / {\n    error_page 404 = @drupal;\n    log_not_found off;\n}\n</code></pre>\n\n<p>And here,</p>\n\n<pre><code>location ~ \\.php$ {\n    try_files $uri @drupal;\n\n    fastcgi_pass ...;\n\n    fastcgi_param SCRIPT_FILENAME /path/to$fastcgi_script_name;\n\n    ...\n}\n</code></pre>\n\n<p><code>try_files</code> checks the existence of the PHP file\nbefore passing the request to the FastCGI server.</p>\n\n<p>Example for Wordpress and Joomla:</p>\n\n<pre><code>location / {\n    try_files $uri $uri/ @wordpress;\n}\n\nlocation ~ \\.php$ {\n    try_files $uri @wordpress;\n\n    fastcgi_pass ...;\n\n    fastcgi_param SCRIPT_FILENAME /path/to$fastcgi_script_name;\n    ... other fastcgi_param's\n}\n\nlocation @wordpress {\n    fastcgi_pass ...;\n\n    fastcgi_param SCRIPT_FILENAME /path/to/index.php;\n    ... other fastcgi_param's\n}\n</code></pre>\n"
