				description_html: "<p>Allows the specified HTTP and WebDAV methods.\nThe parameter <code>off</code> denies all methods processed\nby this module.\nThe following methods are supported:\n<code>PUT</code>, <code>DELETE</code>, <code>MKCOL</code>,\n<code>COPY</code>, and <code>MOVE</code>.</p>\n\n<p>A file uploaded with the PUT method is first written to a temporary file,\nand then the file is renamed.\nStarting from version 0.8.9, temporary files and the persistent store\ncan be put on different file systems.\nHowever, be aware that in this case a file is copied\nacross two file systems instead of the cheap renaming operation.\nIt is thus recommended that for any given location both saved files and a\ndirectory holding temporary files, set by the\n<a href=\"https://nginx.org/en/docs/http/ngx_http_core_module.html#client_body_temp_path\" target=\"_blank\"><code>client_body_temp_path</code></a>\ndirective, are put on the same file system.</p>\n\n<p>When creating a file with the PUT method, it is possible to specify\nthe modification date by passing it in the &ldquo;Date&rdquo;\nheader field.</p>\n"
