				isBlock: false,
				description_md: "Enables or disables buffering of responses from the FastCGI server.\n\nWhen buffering is enabled, nginx receives a response from the FastCGI server\nas soon as possible, saving it into the buffers set by the\n[`fastcgi_buffer_size`](https://nginx.org/en/docs/http/ngx_http_fastcgi_module.html#fastcgi_buffer_size) and [`fastcgi_buffers`](https://nginx.org/en/docs/http/ngx_http_fastcgi_module.html#fastcgi_buffers) directives.\nIf the whole response does not fit into memory, a part of it can be saved\nto a [temporary file](https://nginx.org/en/docs/http/ngx_http_fastcgi_module.html#fastcgi_temp_path) on the disk.\nWriting to temporary files is controlled by the\n[`fastcgi_max_temp_file_size`](https://nginx.org/en/docs/http/ngx_http_fastcgi_module.html#fastcgi_max_temp_file_size) and\n[`fastcgi_temp_file_write_size`](https://nginx.org/en/docs/http/ngx_http_fastcgi_module.html#fastcgi_temp_file_write_size) directives.\n\nWhen buffering is disabled, the response is passed to a client synchronously,\nimmediately as it is received.\nnginx will not try to read the whole response from the FastCGI server.\nThe maximum size of the data that nginx can receive from the server\nat a time is set by the [`fastcgi_buffer_size`](https://nginx.org/en/docs/http/ngx_http_fastcgi_module.html#fastcgi_buffer_size) directive.\n\nBuffering can also be enabled or disabled by passing\n“`yes`” or “`no`” in the\n\"X-Accel-Buffering\" response header field.\nThis capability can be disabled using the\n[`fastcgi_ignore_headers`](https://nginx.org/en/docs/http/ngx_http_fastcgi_module.html#fastcgi_ignore_headers) directive.",
				description_html: "<p>Enables or disables buffering of responses from the FastCGI server.</p>\n\n<p>When buffering is enabled, nginx receives a response from the FastCGI server\nas soon as possible, saving it into the buffers set by the\n<a href=\"https://nginx.org/en/docs/http/ngx_http_fastcgi_module.html#fastcgi_buffer_size\" target=\"_blank\"><code>fastcgi_buffer_size</code></a> and <a href=\"https://nginx.org/en/docs/http/ngx_http_fastcgi_module.html#fastcgi_buffers\" target=\"_blank\"><code>fastcgi_buffers</code></a> directives.\nIf the whole response does not fit into memory, a part of it can be saved\nto a <a href=\"https://nginx.org/en/docs/http/ngx_http_fastcgi_module.html#fastcgi_temp_path\" target=\"_blank\">temporary file</a> on the disk.\nWriting to temporary files is controlled by the\n<a href=\"https://nginx.org/en/docs/http/ngx_http_fastcgi_module.html#fastcgi_max_temp_file_size\" target=\"_blank\"><code>fastcgi_max_temp_file_size</code></a> and\n<a href=\"https://nginx.org/en/docs/http/ngx_http_fastcgi_module.html#fastcgi_temp_file_write_size\" target=\"_blank\"><code>fastcgi_temp_file_write_size</code></a> directives.</p>\n\n<p>When buffering is disabled, the response is passed to a client synchronously,\nimmediately as it is received.\nnginx will not try to read the whole response from the FastCGI server.\nThe maximum size of the data that nginx can receive from the server\nat a time is set by the <a href=\"https://nginx.org/en/docs/http/ngx_http_fastcgi_module.html#fastcgi_buffer_size\" target=\"_blank\"><code>fastcgi_buffer_size</code></a> directive.</p>\n\n<p>Buffering can also be enabled or disabled by passing\n“<code>yes</code>” or “<code>no</code>” in the\n&ldquo;X-Accel-Buffering&rdquo; response header field.\nThis capability can be disabled using the\n<a href=\"https://nginx.org/en/docs/http/ngx_http_fastcgi_module.html#fastcgi_ignore_headers\" target=\"_blank\"><code>fastcgi_ignore_headers</code></a> directive.</p>\n"
