					"<p><em><code>path</code></em> [<code>levels</code>=<em><code>levels</code></em>] [<code>use_temp_path</code>=<code>on</code>|<code>off</code>] <code>keys_zone</code>=<em><code>name</code></em>:<em><code>size</code></em> [<code>inactive</code>=<em><code>time</code></em>] [<code>max_size</code>=<em><code>size</code></em>] [<code>min_free</code>=<em><code>size</code></em>] [<code>manager_files</code>=<em><code>number</code></em>] [<code>manager_sleep</code>=<em><code>time</code></em>] [<code>manager_threshold</code>=<em><code>time</code></em>] [<code>loader_files</code>=<em><code>number</code></em>] [<code>loader_sleep</code>=<em><code>time</code></em>] [<code>loader_threshold</code>=<em><code>time</code></em>] [<code>purger</code>=<code>on</code>|<code>off</code>] [<code>purger_files</code>=<em><code>number</code></em>] [<code>purger_sleep</code>=<em><code>time</code></em>] [<code>purger_threshold</code>=<em><code>time</code></em>]</p>\n"
