				description_md: "Sets a *`parameter`* that should be passed to the FastCGI server.\nThe *`value`* can contain text, variables, and their combination.\nThese directives are inherited from the previous configuration level\nif and only if there are no `fastcgi_param` directives\ndefined on the current level.\n\nThe following example shows the minimum required settings for PHP:\n```\nfastcgi_param SCRIPT_FILENAME /home/www/scripts/php$fastcgi_script_name;\nfastcgi_param QUERY_STRING    $query_string;\n```\n\nThe `SCRIPT_FILENAME` parameter is used in PHP for\ndetermining the script name, and the `QUERY_STRING`\nparameter is used to pass request parameters.\n\nFor scripts that process `POST` requests, the\nfollowing three parameters are also required:\n```\nfastcgi_param REQUEST_METHOD  $request_method;\nfastcgi_param CONTENT_TYPE    $content_type;\nfastcgi_param CONTENT_LENGTH  $content_length;\n```\n\nIf PHP was built with the `--enable-force-cgi-redirect`\nconfiguration parameter, the `REDIRECT_STATUS` parameter\nshould also be passed with the value “200”:\n```\nfastcgi_param REDIRECT_STATUS 200;\n```\n\nIf the directive is specified with `if_not_empty` (1.1.11) then\nsuch a parameter will be passed to the server only if its value is not empty:\n```\nfastcgi_param HTTPS           $https if_not_empty;\n```",
				description_html: "<p>Sets a <em><code>parameter</code></em> that should be passed to the FastCGI server.\nThe <em><code>value</code></em> can contain text, variables, and their combination.\nThese directives are inherited from the previous configuration level\nif and only if there are no <code>fastcgi_param</code> directives\ndefined on the current level.</p>\n\n<p>The following example shows the minimum required settings for PHP:</p>\n\n<pre><code>fastcgi_param SCRIPT_FILENAME /home/www/scripts/php$fastcgi_script_name;\nfastcgi_param QUERY_STRING    $query_string;\n</code></pre>\n\n<p>The <code>SCRIPT_FILENAME</code> parameter is used in PHP for\ndetermining the script name, and the <code>QUERY_STRING</code>\nparameter is used to pass request parameters.</p>\n\n<p>For scripts that process <code>POST</code> requests, the\nfollowing three parameters are also required:</p>\n\n<pre><code>fastcgi_param REQUEST_METHOD  $request_method;\nfastcgi_param CONTENT_TYPE    $content_type;\nfastcgi_param CONTENT_LENGTH  $content_length;\n</code></pre>\n\n<p>If PHP was built with the <code>--enable-force-cgi-redirect</code>\nconfiguration parameter, the <code>REDIRECT_STATUS</code> parameter\nshould also be passed with the value “200”:</p>\n\n<pre><code>fastcgi_param REDIRECT_STATUS 200;\n</code></pre>\n\n<p>If the directive is specified with <code>if_not_empty</code> (1.1.11) then\nsuch a parameter will be passed to the server only if its value is not empty:</p>\n\n<pre><code>fastcgi_param HTTPS           $https if_not_empty;\n</code></pre>\n"
