				isBlock: false,
				description_md: "Specifies in which cases a request should be passed to the next server:\n- `error`\n\n    an error occurred while establishing a connection with the\n    server, passing a request to it, or reading the response header;\n- `timeout`\n\n    a timeout has occurred while establishing a connection with the\n    server, passing a request to it, or reading the response header;\n- `invalid_header`\n\n    a server returned an empty or invalid response;\n- `http_500`\n\n    a server returned a response with the code 500;\n- `http_502`\n\n    a server returned a response with the code 502;\n- `http_503`\n\n    a server returned a response with the code 503;\n- `http_504`\n\n    a server returned a response with the code 504;\n- `http_403`\n\n    a server returned a response with the code 403;\n- `http_404`\n\n    a server returned a response with the code 404;\n- `http_429`\n\n    a server returned a response with the code 429;\n- `non_idempotent`\n\n    normally, requests with a\n    [non-idempotent](https://datatracker.ietf.org/doc/html/rfc7231#section-4.2.2)\n    method\n    (`POST`, `LOCK`, `PATCH`)\n    are not passed to the next server\n    if a request has been sent to an upstream server;\n    enabling this option explicitly allows retrying such requests;\n- `off`\n\n    disables passing a request to the next server.\n\nOne should bear in mind that passing a request to the next server is\nonly possible if nothing has been sent to a client yet.\nThat is, if an error or timeout occurs in the middle of the\ntransferring of a response, fixing this is impossible.\n\nThe directive also defines what is considered an\n[unsuccessful attempt](https://nginx.org/en/docs/http/ngx_http_upstream_module.html#max_fails) of communication with a server.\nThe cases of `error`, `timeout` and\n`invalid_header` are always considered unsuccessful attempts,\neven if they are not specified in the directive.\nThe cases of `http_500`, `http_502`,\n`http_503`, `http_504`,\nand `http_429` are\nconsidered unsuccessful attempts only if they are specified in the directive.\nThe cases of `http_403` and `http_404`\nare never considered unsuccessful attempts.\n\nPassing a request to the next server can be limited by\n[the number of tries](https://nginx.org/en/docs/http/ngx_http_grpc_module.html#grpc_next_upstream_tries)\nand by [time](https://nginx.org/en/docs/http/ngx_http_grpc_module.html#grpc_next_upstream_timeout).",
				description_html: "<p>Specifies in which cases a request should be passed to the next server:</p>\n\n<ul>\n<li><p><code>error</code></p>\n\n<p>an error occurred while establishing a connection with the\nserver, passing a request to it, or reading the response header;</p></li>\n\n<li><p><code>timeout</code></p>\n\n<p>a timeout has occurred while establishing a connection with the\nserver, passing a request to it, or reading the response header;</p></li>\n\n<li><p><code>invalid_header</code></p>\n\n<p>a server returned an empty or invalid response;</p></li>\n\n<li><p><code>http_500</code></p>\n\n<p>a server returned a response with the code 500;</p></li>\n\n<li><p><code>http_502</code></p>\n\n<p>a server returned a response with the code 502;</p></li>\n\n<li><p><code>http_503</code></p>\n\n<p>a server returned a response with the code 503;</p></li>\n\n<li><p><code>http_504</code></p>\n\n<p>a server returned a response with the code 504;</p></li>\n\n<li><p><code>http_403</code></p>\n\n<p>a server returned a response with the code 403;</p></li>\n\n<li><p><code>http_404</code></p>\n\n<p>a server returned a response with the code 404;</p></li>\n\n<li><p><code>http_429</code></p>\n\n<p>a server returned a response with the code 429;</p></li>\n\n<li><p><code>non_idempotent</code></p>\n\n<p>normally, requests with a\n<a href=\"https://datatracker.ietf.org/doc/html/rfc7231#section-4.2.2\" target=\"_blank\">non-idempotent</a>\nmethod\n(<code>POST</code>, <code>LOCK</code>, <code>PATCH</code>)\nare not passed to the next server\nif a request has been sent to an upstream server;\nenabling this option explicitly allows retrying such requests;</p></li>\n\n<li><p><code>off</code></p>\n\n<p>disables passing a request to the next server.</p></li>\n</ul>\n\n<p>One should bear in mind that passing a request to the next server is\nonly possible if nothing has been sent to a client yet.\nThat is, if an error or timeout occurs in the middle of the\ntransferring of a response, fixing this is impossible.</p>\n\n<p>The directive also defines what is considered an\n<a href=\"https://nginx.org/en/docs/http/ngx_http_upstream_module.html#max_fails\" target=\"_blank\">unsuccessful attempt</a> of communication with a server.\nThe cases of <code>error</code>, <code>timeout</code> and\n<code>invalid_header</code> are always considered unsuccessful attempts,\neven if they are not specified in the directive.\nThe cases of <code>http_500</code>, <code>http_502</code>,\n<code>http_503</code>, <code>http_504</code>,\nand <code>http_429</code> are\nconsidered unsuccessful attempts only if they are specified in the directive.\nThe cases of <code>http_403</code> and <code>http_404</code>\nare never considered unsuccessful attempts.</p>\n\n<p>Passing a request to the next server can be limited by\n<a href=\"https://nginx.org/en/docs/http/ngx_http_grpc_module.html#grpc_next_upstream_tries\" target=\"_blank\">the number of tries</a>\nand by <a href=\"https://nginx.org/en/docs/http/ngx_http_grpc_module.html#grpc_next_upstream_timeout\" target=\"_blank\">time</a>.</p>\n"
