				description_md: "Adds arguments from a playlist request to URIs of fragments.\nThis may be useful for performing client authorization at the moment of\nrequesting a fragment, or when protecting an HLS stream with the\n[ngx_http_secure_link_module](https://nginx.org/en/docs/http/ngx_http_secure_link_module.html)\nmodule.\n\nFor example, if a client requests a playlist\n`http://example.com/hls/test.mp4.m3u8?a=1&b=2`,\nthe arguments `a=1` and `b=2`\nwill be added to URIs of fragments after the arguments\n`start` and `end`:\n```\n#EXTM3U\n#EXT-X-VERSION:3\n#EXT-X-TARGETDURATION:15\n#EXT-X-PLAYLIST-TYPE:VOD\n\n#EXTINF:9.333,\ntest.mp4.ts?start=0.000&end=9.333&a=1&b=2\n#EXTINF:7.167,\ntest.mp4.ts?start=9.333&end=16.500&a=1&b=2\n#EXTINF:5.416,\ntest.mp4.ts?start=16.500&end=21.916&a=1&b=2\n#EXTINF:5.500,\ntest.mp4.ts?start=21.916&end=27.416&a=1&b=2\n#EXTINF:15.167,\ntest.mp4.ts?start=27.416&end=42.583&a=1&b=2\n#EXTINF:9.626,\ntest.mp4.ts?start=42.583&end=52.209&a=1&b=2\n\n#EXT-X-ENDLIST\n```\n\nIf an HLS stream is protected with the\n[ngx_http_secure_link_module](https://nginx.org/en/docs/http/ngx_http_secure_link_module.html)\nmodule, `$uri` should not be used in the\n[`secure_link_md5`](https://nginx.org/en/docs/http/ngx_http_secure_link_module.html#secure_link_md5)\nexpression because this will cause errors when requesting the fragments.\n[Base URI](https://nginx.org/en/docs/http/ngx_http_map_module.html#map) should be used\ninstead of `$uri`\n(`$hls_uri` in the example):\n```\nhttp {\n    ...\n\n    map $uri $hls_uri {\n        ~^(?<base_uri>.*).m3u8$ $base_uri;\n        ~^(?<base_uri>.*).ts$   $base_uri;\n        default                 $uri;\n    }\n\n    server {\n        ...\n\n        location /hls/ {\n            hls;\n            hls_forward_args on;\n\n            alias /var/videos/;\n\n            secure_link $arg_md5,$arg_expires;\n            secure_link_md5 \"$secure_link_expires$hls_uri$remote_addr secret\";\n\n            if ($secure_link = \"\") {\n                return 403;\n            }\n\n            if ($secure_link = \"0\") {\n                return 410;\n            }\n        }\n    }\n}\n```",
				description_html: "<p>Adds arguments from a playlist request to URIs of fragments.\nThis may be useful for performing client authorization at the moment of\nrequesting a fragment, or when protecting an HLS stream with the\n<a href=\"https://nginx.org/en/docs/http/ngx_http_secure_link_module.html\" target=\"_blank\">ngx_http_secure_link_module</a>\nmodule.</p>\n\n<p>For example, if a client requests a playlist\n<code>http://example.com/hls/test.mp4.m3u8?a=1&amp;b=2</code>,\nthe arguments <code>a=1</code> and <code>b=2</code>\nwill be added to URIs of fragments after the arguments\n<code>start</code> and <code>end</code>:</p>\n\n<pre><code>#EXTM3U\n#EXT-X-VERSION:3\n#EXT-X-TARGETDURATION:15\n#EXT-X-PLAYLIST-TYPE:VOD\n\n#EXTINF:9.333,\ntest.mp4.ts?start=0.000&amp;end=9.333&amp;a=1&amp;b=2\n#EXTINF:7.167,\ntest.mp4.ts?start=9.333&amp;end=16.500&amp;a=1&amp;b=2\n#EXTINF:5.416,\ntest.mp4.ts?start=16.500&amp;end=21.916&amp;a=1&amp;b=2\n#EXTINF:5.500,\ntest.mp4.ts?start=21.916&amp;end=27.416&amp;a=1&amp;b=2\n#EXTINF:15.167,\ntest.mp4.ts?start=27.416&amp;end=42.583&amp;a=1&amp;b=2\n#EXTINF:9.626,\ntest.mp4.ts?start=42.583&amp;end=52.209&amp;a=1&amp;b=2\n\n#EXT-X-ENDLIST\n</code></pre>\n\n<p>If an HLS stream is protected with the\n<a href=\"https://nginx.org/en/docs/http/ngx_http_secure_link_module.html\" target=\"_blank\">ngx_http_secure_link_module</a>\nmodule, <code>$uri</code> should not be used in the\n<a href=\"https://nginx.org/en/docs/http/ngx_http_secure_link_module.html#secure_link_md5\" target=\"_blank\"><code>secure_link_md5</code></a>\nexpression because this will cause errors when requesting the fragments.\n<a href=\"https://nginx.org/en/docs/http/ngx_http_map_module.html#map\" target=\"_blank\">Base URI</a> should be used\ninstead of <code>$uri</code>\n(<code>$hls_uri</code> in the example):</p>\n\n<pre><code>http {\n    ...\n\n    map $uri $hls_uri {\n        ~^(?&lt;base_uri&gt;.*).m3u8$ $base_uri;\n        ~^(?&lt;base_uri&gt;.*).ts$   $base_uri;\n        default                 $uri;\n    }\n\n    server {\n        ...\n\n        location /hls/ {\n            hls;\n            hls_forward_args on;\n\n            alias /var/videos/;\n\n            secure_link $arg_md5,$arg_expires;\n            secure_link_md5 &quot;$secure_link_expires$hls_uri$remote_addr secret&quot;;\n\n            if ($secure_link = &quot;&quot;) {\n                return 403;\n            }\n\n            if ($secure_link = &quot;0&quot;) {\n                return 410;\n            }\n        }\n    }\n}\n</code></pre>\n"
