				isBlock: false,
				description_md: "Sets the shared memory zone\nand the maximum burst size of requests.\nIf the requests rate exceeds the rate configured for a zone,\ntheir processing is delayed such that requests are processed\nat a defined rate.\nExcessive requests are delayed until their number exceeds the\nmaximum burst size\nin which case the request is terminated with an\n[error](https://nginx.org/en/docs/http/ngx_http_limit_req_module.html#limit_req_status).\nBy default, the maximum burst size is equal to zero.\nFor example, the directives\n```\nlimit_req_zone $binary_remote_addr zone=one:10m rate=1r/s;\n\nserver {\n    location /search/ {\n        limit_req zone=one burst=5;\n    }\n```\nallow not more than 1 request per second at an average,\nwith bursts not exceeding 5 requests.\n\nIf delaying of excessive requests while requests are being limited is not\ndesired, the parameter `nodelay` should be used:\n```\nlimit_req zone=one burst=5 nodelay;\n```\n\nThe `delay` parameter (1.15.7) specifies a limit\nat which excessive requests become delayed.\nDefault value is zero, i.e. all excessive requests are delayed.\n\nThere could be several `limit_req` directives.\nFor example, the following configuration will limit the processing rate\nof requests coming from a single IP address and, at the same time,\nthe request processing rate by the virtual server:\n```\nlimit_req_zone $binary_remote_addr zone=perip:10m rate=1r/s;\nlimit_req_zone $server_name zone=perserver:10m rate=10r/s;\n\nserver {\n    ...\n    limit_req zone=perip burst=5 nodelay;\n    limit_req zone=perserver burst=10;\n}\n```\n\nThese directives are inherited from the previous configuration level\nif and only if there are no `limit_req` directives\ndefined on the current level.",
				description_html: "<p>Sets the shared memory zone\nand the maximum burst size of requests.\nIf the requests rate exceeds the rate configured for a zone,\ntheir processing is delayed such that requests are processed\nat a defined rate.\nExcessive requests are delayed until their number exceeds the\nmaximum burst size\nin which case the request is terminated with an\n<a href=\"https://nginx.org/en/docs/http/ngx_http_limit_req_module.html#limit_req_status\" target=\"_blank\">error</a>.\nBy default, the maximum burst size is equal to zero.\nFor example, the directives</p>\n\n<pre><code>limit_req_zone $binary_remote_addr zone=one:10m rate=1r/s;\n\nserver {\n    location /search/ {\n        limit_req zone=one burst=5;\n    }\n</code></pre>\n\n<p>allow not more than 1 request per second at an average,\nwith bursts not exceeding 5 requests.</p>\n\n<p>If delaying of excessive requests while requests are being limited is not\ndesired, the parameter <code>nodelay</code> should be used:</p>\n\n<pre><code>limit_req zone=one burst=5 nodelay;\n</code></pre>\n\n<p>The <code>delay</code> parameter (1.15.7) specifies a limit\nat which excessive requests become delayed.\nDefault value is zero, i.e. all excessive requests are delayed.</p>\n\n<p>There could be several <code>limit_req</code> directives.\nFor example, the following configuration will limit the processing rate\nof requests coming from a single IP address and, at the same time,\nthe request processing rate by the virtual server:</p>\n\n<pre><code>limit_req_zone $binary_remote_addr zone=perip:10m rate=1r/s;\nlimit_req_zone $server_name zone=perserver:10m rate=10r/s;\n\nserver {\n    ...\n    limit_req zone=perip burst=5 nodelay;\n    limit_req zone=perserver burst=10;\n}\n</code></pre>\n\n<p>These directives are inherited from the previous configuration level\nif and only if there are no <code>limit_req</code> directives\ndefined on the current level.</p>\n"
