				syntax_md: [
					"*`string`* *`$variable`* `{...}`"
				],
				syntax_html: [
					"<p><em><code>string</code></em> <em><code>$variable</code></em> <code>{...}</code></p>\n"
				],
				isBlock: true,
				description_md: "Creates a new variable whose value\ndepends on values of one or more of the source variables\nspecified in the first parameter.\n> Before version 0.9.0 only a single variable could be\n> specified in the first parameter.\n\n> Since variables are evaluated only when they are used, the mere declaration\n> even of a large number of “`map`” variables\n> does not add any extra costs to request processing.\n\nParameters inside the `map` block specify a mapping\nbetween source and resulting values.\n\nSource values are specified as strings or regular expressions (0.9.6).\n\nStrings are matched ignoring the case.\n\nA regular expression should either start from the “`~`”\nsymbol for a case-sensitive matching, or from the “`~*`”\nsymbols (1.0.4) for case-insensitive matching.\nA regular expression can contain named and positional captures\nthat can later be used in other directives along with the\nresulting variable.\n\nIf a source value matches one of the names of special parameters\ndescribed below, it should be prefixed with the “`\\`” symbol.\n\nThe resulting value can contain text,\nvariable (0.9.0), and their combination (1.11.0).\n\nThe following special parameters are also supported:\n- `default` *`value`*\n\n    sets the resulting value if the source value matches none\n    of the specified variants.\n    When `default` is not specified, the default\n    resulting value will be an empty string.\n- `hostnames`\n\n    indicates that source values can be hostnames with a prefix or suffix mask:\n    ```\n    *.example.com 1;\n    example.*     1;\n    ```\n    The following two records\n    ```\n    example.com   1;\n    *.example.com 1;\n    ```\n    can be combined:\n    ```\n    .example.com  1;\n    ```\n    This parameter should be specified before the list of values.\n- `include` *`file`*\n\n    includes a file with values.\n    There can be several inclusions.\n- `volatile`\n\n    indicates that the variable is not cacheable (1.11.7).\n\nIf the source value matches more than one of the specified variants,\ne.g. both a mask and a regular expression match, the first matching\nvariant will be chosen, in the following order of priority:\n1. string value without a mask\n2. longest string value with a prefix mask,\n    e.g. “`*.example.com`”\n3. longest string value with a suffix mask,\n    e.g. “`mail.*`”\n4. first matching regular expression\n    (in order of appearance in a configuration file)\n5. default value",
				description_html: "<p>Creates a new variable whose value\ndepends on values of one or more of the source variables\nspecified in the first parameter.</p>\n\n<blockquote>\n<p>Before version 0.9.0 only a single variable could be\nspecified in the first parameter.</p>\n\n<p>Since variables are evaluated only when they are used, the mere declaration\neven of a large number of “<code>map</code>” variables\ndoes not add any extra costs to request processing.</p>\n</blockquote>\n\n<p>Parameters inside the <code>map</code> block specify a mapping\nbetween source and resulting values.</p>\n\n<p>Source values are specified as strings or regular expressions (0.9.6).</p>\n\n<p>Strings are matched ignoring the case.</p>\n\n<p>A regular expression should either start from the “<code>~</code>”\nsymbol for a case-sensitive matching, or from the “<code>~*</code>”\nsymbols (1.0.4) for case-insensitive matching.\nA regular expression can contain named and positional captures\nthat can later be used in other directives along with the\nresulting variable.</p>\n\n<p>If a source value matches one of the names of special parameters\ndescribed below, it should be prefixed with the “<code>\\</code>” symbol.</p>\n\n<p>The resulting value can contain text,\nvariable (0.9.0), and their combination (1.11.0).</p>\n\n<p>The following special parameters are also supported:</p>\n\n<ul>\n<li><p><code>default</code> <em><code>value</code></em></p>\n\n<p>sets the resulting value if the source value matches none\nof the specified variants.\nWhen <code>default</code> is not specified, the default\nresulting value will be an empty string.</p></li>\n\n<li><p><code>hostnames</code></p>\n\n<p>indicates that source values can be hostnames with a prefix or suffix mask:</p>\n\n<pre><code>*.example.com 1;\nexample.*     1;\n</code></pre>\n<p>The following two records</p>\n\n<pre><code>example.com   1;\n*.example.com 1;\n</code></pre>\n<p>can be combined:</p>\n\n<pre><code>.example.com  1;\n</code></pre>\n<p>This parameter should be specified before the list of values.</p></li>\n\n<li><p><code>include</code> <em><code>file</code></em></p>\n\n<p>includes a file with values.\nThere can be several inclusions.</p></li>\n\n<li><p><code>volatile</code></p>\n\n<p>indicates that the variable is not cacheable (1.11.7).</p></li>\n</ul>\n\n<p>If the source value matches more than one of the specified variants,\ne.g. both a mask and a regular expression match, the first matching\nvariant will be chosen, in the following order of priority:</p>\n\n<ol>\n<li>string value without a mask</li>\n<li>longest string value with a prefix mask,\ne.g. “<code>*.example.com</code>”</li>\n<li>longest string value with a suffix mask,\ne.g. “<code>mail.*</code>”</li>\n<li>first matching regular expression\n(in order of appearance in a configuration file)</li>\n<li>default value</li>\n</ol>\n"
