				syntax_html: [
					"<p><em><code>address</code></em> [<code>transparent</code>] | <code>off</code></p>\n"
				],
				isBlock: false,
				description_md: "Makes outgoing connections to a memcached server originate\nfrom the specified local IP address with an optional port (1.11.2).\nParameter value can contain variables (1.3.12).\nThe special value `off` (1.3.12) cancels the effect\nof the `memcached_bind` directive\ninherited from the previous configuration level, which allows the\nsystem to auto-assign the local IP address and port.\n\nThe `transparent` parameter (1.11.0) allows\noutgoing connections to a memcached server originate\nfrom a non-local IP address,\nfor example, from a real IP address of a client:\n```\nmemcached_bind $remote_addr transparent;\n```\nIn order for this parameter to work,\nit is usually necessary to run nginx worker processes with the\n[superuser](https://nginx.org/en/docs/ngx_core_module.html#user) privileges.\nOn Linux it is not required (1.13.8) as if\nthe `transparent` parameter is specified, worker processes\ninherit the `CAP_NET_RAW` capability from the master process.\nIt is also necessary to configure kernel routing table\nto intercept network traffic from the memcached server.",
				description_html: "<p>Makes outgoing connections to a memcached server originate\nfrom the specified local IP address with an optional port (1.11.2).\nParameter value can contain variables (1.3.12).\nThe special value <code>off</code> (1.3.12) cancels the effect\nof the <code>memcached_bind</code> directive\ninherited from the previous configuration level, which allows the\nsystem to auto-assign the local IP address and port.</p>\n\n<p>The <code>transparent</code> parameter (1.11.0) allows\noutgoing connections to a memcached server originate\nfrom a non-local IP address,\nfor example, from a real IP address of a client:</p>\n\n<pre><code>memcached_bind $remote_addr transparent;\n</code></pre>\n\n<p>In order for this parameter to work,\nit is usually necessary to run nginx worker processes with the\n<a href=\"https://nginx.org/en/docs/ngx_core_module.html#user\" target=\"_blank\">superuser</a> privileges.\nOn Linux it is not required (1.13.8) as if\nthe <code>transparent</code> parameter is specified, worker processes\ninherit the <code>CAP_NET_RAW</code> capability from the master process.\nIt is also necessary to configure kernel routing table\nto intercept network traffic from the memcached server.</p>\n"
