				isBlock: false,
				description_md: "Sets the URI to which an original request will be mirrored.\nSeveral mirrors can be specified on the same configuration level.",
				description_html: "<p>Sets the URI to which an original request will be mirrored.\nSeveral mirrors can be specified on the same configuration level.</p>\n"
