				syntax_html: [
					"<p>string &hellip;</p>\n"
				],
				isBlock: false,
				description_md: "Defines conditions under which the request will be considered a cache\npurge request.\nIf at least one value of the string parameters is not empty and is not equal\nto “0” then the cache entry with a corresponding\n[cache key](https://nginx.org/en/docs/http/ngx_http_proxy_module.html#proxy_cache_key) is removed.\nThe result of successful operation is indicated by returning\nthe 204 (No Content) response.\n\nIf the [cache key](https://nginx.org/en/docs/http/ngx_http_proxy_module.html#proxy_cache_key) of a purge request ends\nwith an asterisk (“`*`”), all cache entries matching the\nwildcard key will be removed from the cache.\nHowever, these entries will remain on the disk until they are deleted\nfor either [inactivity](https://nginx.org/en/docs/http/ngx_http_proxy_module.html#proxy_cache_path),\nor processed by the [cache purger](https://nginx.org/en/docs/http/ngx_http_proxy_module.html#purger) (1.7.12),\nor a client attempts to access them.\n\nExample configuration:\n```\nproxy_cache_path /data/nginx/cache keys_zone=cache_zone:10m;\n\nmap $request_method $purge_method {\n    PURGE   1;\n    default 0;\n}\n\nserver {\n    ...\n    location / {\n        proxy_pass http://backend;\n        proxy_cache cache_zone;\n        proxy_cache_key $uri;\n        proxy_cache_purge $purge_method;\n    }\n}\n```\n> This functionality is available as part of our\n> [commercial subscription](https://nginx.com/products/).",
				description_html: "<p>Defines conditions under which the request will be considered a cache\npurge request.\nIf at least one value of the string parameters is not empty and is not equal\nto “0” then the cache entry with a corresponding\n<a href=\"https://nginx.org/en/docs/http/ngx_http_proxy_module.html#proxy_cache_key\" target=\"_blank\">cache key</a> is removed.\nThe result of successful operation is indicated by returning\nthe 204 (No Content) response.</p>\n\n<p>If the <a href=\"https://nginx.org/en/docs/http/ngx_http_proxy_module.html#proxy_cache_key\" target=\"_blank\">cache key</a> of a purge request ends\nwith an asterisk (“<code>*</code>”), all cache entries matching the\nwildcard key will be removed from the cache.\nHowever, these entries will remain on the disk until they are deleted\nfor either <a href=\"https://nginx.org/en/docs/http/ngx_http_proxy_module.html#proxy_cache_path\" target=\"_blank\">inactivity</a>,\nor processed by the <a href=\"https://nginx.org/en/docs/http/ngx_http_proxy_module.html#purger\" target=\"_blank\">cache purger</a> (1.7.12),\nor a client attempts to access them.</p>\n\n<p>Example configuration:</p>\n\n<pre><code>proxy_cache_path /data/nginx/cache keys_zone=cache_zone:10m;\n\nmap $request_method $purge_method {\n    PURGE   1;\n    default 0;\n}\n\nserver {\n    ...\n    location / {\n        proxy_pass http://backend;\n        proxy_cache cache_zone;\n        proxy_cache_key $uri;\n        proxy_cache_purge $purge_method;\n    }\n}\n</code></pre>\n\n<blockquote>\n<p>This functionality is available as part of our\n<a href=\"https://nginx.com/products/\" target=\"_blank\">commercial subscription</a>.</p>\n</blockquote>\n"
