				syntax_md: [
					"*`URL`*"
				],
				syntax_html: [
					"<p><em><code>URL</code></em></p>\n"
				],
				isBlock: false,
				description_md: "Sets the protocol and address of a proxied server and an optional URI\nto which a location should be mapped.\nAs a protocol, “`http`” or “`https`”\ncan be specified.\nThe address can be specified as a domain name or IP address,\nand an optional port:\n```\nproxy_pass http://localhost:8000/uri/;\n```\nor as a UNIX-domain socket path specified after the word\n“`unix`” and enclosed in colons:\n```\nproxy_pass http://unix:/tmp/backend.socket:/uri/;\n```\n\nIf a domain name resolves to several addresses, all of them will be\nused in a round-robin fashion.\nIn addition, an address can be specified as a\n[server group](https://nginx.org/en/docs/http/ngx_http_upstream_module.html).\n\nParameter value can contain variables.\nIn this case, if an address is specified as a domain name,\nthe name is searched among the described server groups,\nand, if not found, is determined using a\n[`resolver`](https://nginx.org/en/docs/http/ngx_http_core_module.html#resolver).\n\nA request URI is passed to the server as follows:\n- If the `proxy_pass` directive is specified with a URI,\n    then when a request is passed to the server, the part of a\n    [normalized](https://nginx.org/en/docs/http/ngx_http_core_module.html#location)\n    request URI matching the location is replaced by a URI\n    specified in the directive:\n    ```\n    location /name/ {\n        proxy_pass http://127.0.0.1/remote/;\n    }\n    ```\n- If `proxy_pass` is specified without a URI,\n    the request URI is passed to the server in the same form\n    as sent by a client when the original request is processed,\n    or the full normalized request URI is passed\n    when processing the changed URI:\n    ```\n    location /some/path/ {\n        proxy_pass http://127.0.0.1;\n    }\n    ```\n    > Before version 1.1.12,\n    > if `proxy_pass` is specified without a URI,\n    > the original request URI might be passed\n    > instead of the changed URI in some cases.\n\nIn some cases, the part of a request URI to be replaced cannot be determined:\n- When location is specified using a regular expression,\n    and also inside named locations.\n    \n    In these cases,\n    `proxy_pass` should be specified without a URI.\n- When the URI is changed inside a proxied location using the\n    [`rewrite`](https://nginx.org/en/docs/http/ngx_http_rewrite_module.html#rewrite) directive,\n    and this same configuration will be used to process a request\n    (`break`):\n    ```\n    location /name/ {\n        rewrite    /name/([^/]+) /users?name=$1 break;\n        proxy_pass http://127.0.0.1;\n    }\n    ```\n    \n    In this case, the URI specified in the directive is ignored and\n    the full changed request URI is passed to the server.\n- When variables are used in `proxy_pass`:\n    ```\n    location /name/ {\n        proxy_pass http://127.0.0.1$request_uri;\n    }\n    ```\n    In this case, if URI is specified in the directive,\n    it is passed to the server as is,\n    replacing the original request URI.\n\n[WebSocket](https://nginx.org/en/docs/http/websocket.html) proxying requires special\nconfiguration and is supported since version 1.3.13.",
				description_html: "<p>Sets the protocol and address of a proxied server and an optional URI\nto which a location should be mapped.\nAs a protocol, “<code>http</code>” or “<code>https</code>”\ncan be specified.\nThe address can be specified as a domain name or IP address,\nand an optional port:</p>\n\n<pre><code>proxy_pass http://localhost:8000/uri/;\n</code></pre>\n\n<p>or as a UNIX-domain socket path specified after the word\n“<code>unix</code>” and enclosed in colons:</p>\n\n<pre><code>proxy_pass http://unix:/tmp/backend.socket:/uri/;\n</code></pre>\n\n<p>If a domain name resolves to several addresses, all of them will be\nused in a round-robin fashion.\nIn addition, an address can be specified as a\n<a href=\"https://nginx.org/en/docs/http/ngx_http_upstream_module.html\" target=\"_blank\">server group</a>.</p>\n\n<p>Parameter value can contain variables.\nIn this case, if an address is specified as a domain name,\nthe name is searched among the described server groups,\nand, if not found, is determined using a\n<a href=\"https://nginx.org/en/docs/http/ngx_http_core_module.html#resolver\" target=\"_blank\"><code>resolver</code></a>.</p>\n\n<p>A request URI is passed to the server as follows:</p>\n\n<ul>\n<li>If the <code>proxy_pass</code> directive is specified with a URI,\nthen when a request is passed to the server, the part of a\n<a href=\"https://nginx.org/en/docs/http/ngx_http_core_module.html#location\" target=\"_blank\">normalized</a>\nrequest URI matching the location is replaced by a URI\nspecified in the directive:\n\n<pre><code>\nlocation /name/ {\n    proxy_pass http://127.0.0.1/remote/;\n}\n</code></pre>\n</li>\n<li>If <code>proxy_pass</code> is specified without a URI,\nthe request URI is passed to the server in the same form\nas sent by a client when the original request is processed,\nor the full normalized request URI is passed\nwhen processing the changed URI:\n\n<pre><code>\nlocation /some/path/ {\n    proxy_pass http://127.0.0.1;\n}\n</code></pre>\n\n&gt; Before version 1.1.12,\n&gt; if <code>proxy_pass</code> is specified without a URI,\n&gt; the original request URI might be passed\n&gt; instead of the changed URI in some cases.</li>\n</ul>\n\n<p>In some cases, the part of a request URI to be replaced cannot be determined:</p>\n\n<ul>\n<li><p>When location is specified using a regular expression,\nand also inside named locations.</p>\n\n<p>In these cases,\n<code>proxy_pass</code> should be specified without a URI.</p></li>\n\n<li><p>When the URI is changed inside a proxied location using the\n<a href=\"https://nginx.org/en/docs/http/ngx_http_rewrite_module.html#rewrite\" target=\"_blank\"><code>rewrite</code></a> directive,\nand this same configuration will be used to process a request\n(<code>break</code>):</p>\n\n<pre><code>location /name/ {\n    rewrite    /name/([^/]+) /users?name=$1 break;\n    proxy_pass http://127.0.0.1;\n}\n</code></pre>\n<p>In this case, the URI specified in the directive is ignored and\nthe full changed request URI is passed to the server.</p></li>\n\n<li><p>When variables are used in <code>proxy_pass</code>:</p>\n\n<pre><code>location /name/ {\n    proxy_pass http://127.0.0.1$request_uri;\n}\n</code></pre>\n<p>In this case, if URI is specified in the directive,\nit is passed to the server as is,\nreplacing the original request URI.</p></li>\n</ul>\n\n<p><a href=\"https://nginx.org/en/docs/http/websocket.html\" target=\"_blank\">WebSocket</a> proxying requires special\nconfiguration and is supported since version 1.3.13.</p>\n"
