				isBlock: false,
				description_md: "Sets the text that should be changed in the \"Location\"\nand \"Refresh\" header fields of a proxied server response.\nSuppose a proxied server returned the header field\n“`Location: http://localhost:8000/two/some/uri/`”.\nThe directive\n```\nproxy_redirect http://localhost:8000/two/ http://frontend/one/;\n```\nwill rewrite this string to\n“`Location: http://frontend/one/some/uri/`”.\n\nA server name may be omitted in the *`replacement`* string:\n```\nproxy_redirect http://localhost:8000/two/ /;\n```\nthen the primary server’s name and port, if different from 80,\nwill be inserted.\n\nThe default replacement specified by the `default` parameter\nuses the parameters of the\n[`location`](https://nginx.org/en/docs/http/ngx_http_core_module.html#location) and\n[`proxy_pass`](https://nginx.org/en/docs/http/ngx_http_proxy_module.html#proxy_pass) directives.\nHence, the two configurations below are equivalent:\n```\nlocation /one/ {\n    proxy_pass     http://upstream:port/two/;\n    proxy_redirect default;\n```\n\n```\nlocation /one/ {\n    proxy_pass     http://upstream:port/two/;\n    proxy_redirect http://upstream:port/two/ /one/;\n```\nThe `default` parameter is not permitted if\n[`proxy_pass`](https://nginx.org/en/docs/http/ngx_http_proxy_module.html#proxy_pass) is specified using variables.\n\nA *`replacement`* string can contain variables:\n```\nproxy_redirect http://localhost:8000/ http://$host:$server_port/;\n```\n\nA *`redirect`* can also contain (1.1.11) variables:\n```\nproxy_redirect http://$proxy_host:8000/ /;\n```\n\nThe directive can be specified (1.1.11) using regular expressions.\nIn this case, *`redirect`* should either start with\nthe “`~`” symbol for a case-sensitive matching,\nor with the “`~*`” symbols for case-insensitive\nmatching.\nThe regular expression can contain named and positional captures,\nand *`replacement`* can reference them:\n```\nproxy_redirect ~^(http://[^:]+):\\d+(/.+)$ $1$2;\nproxy_redirect ~*/user/([^/]+)/(.+)$      http://$1.example.com/$2;\n```\n\nSeveral `proxy_redirect` directives\ncan be specified on the same level:\n```\nproxy_redirect default;\nproxy_redirect http://localhost:8000/  /;\nproxy_redirect http://www.example.com/ /;\n```\nIf several directives can be applied to\nthe header fields of a proxied server response,\nthe first matching directive will be chosen.\n\nThe `off` parameter cancels the effect\nof the `proxy_redirect` directives\ninherited from the previous configuration level.\n\nUsing this directive, it is also possible to add host names to relative\nredirects issued by a proxied server:\n```\nproxy_redirect / /;\n```",
				description_html: "<p>Sets the text that should be changed in the &ldquo;Location&rdquo;\nand &ldquo;Refresh&rdquo; header fields of a proxied server response.\nSuppose a proxied server returned the header field\n“<code>Location: http://localhost:8000/two/some/uri/</code>”.\nThe directive</p>\n\n<pre><code>proxy_redirect http://localhost:8000/two/ http://frontend/one/;\n</code></pre>\n\n<p>will rewrite this string to\n“<code>Location: http://frontend/one/some/uri/</code>”.</p>\n\n<p>A server name may be omitted in the <em><code>replacement</code></em> string:</p>\n\n<pre><code>proxy_redirect http://localhost:8000/two/ /;\n</code></pre>\n\n<p>then the primary server’s name and port, if different from 80,\nwill be inserted.</p>\n\n<p>The default replacement specified by the <code>default</code> parameter\nuses the parameters of the\n<a href=\"https://nginx.org/en/docs/http/ngx_http_core_module.html#location\" target=\"_blank\"><code>location</code></a> and\n<a href=\"https://nginx.org/en/docs/http/ngx_http_proxy_module.html#proxy_pass\" target=\"_blank\"><code>proxy_pass</code></a> directives.\nHence, the two configurations below are equivalent:</p>\n\n<pre><code>location /one/ {\n    proxy_pass     http://upstream:port/two/;\n    proxy_redirect default;\n</code></pre>\n\n<pre><code>location /one/ {\n    proxy_pass     http://upstream:port/two/;\n    proxy_redirect http://upstream:port/two/ /one/;\n</code></pre>\n\n<p>The <code>default</code> parameter is not permitted if\n<a href=\"https://nginx.org/en/docs/http/ngx_http_proxy_module.html#proxy_pass\" target=\"_blank\"><code>proxy_pass</code></a> is specified using variables.</p>\n\n<p>A <em><code>replacement</code></em> string can contain variables:</p>\n\n<pre><code>proxy_redirect http://localhost:8000/ http://$host:$server_port/;\n</code></pre>\n\n<p>A <em><code>redirect</code></em> can also contain (1.1.11) variables:</p>\n\n<pre><code>proxy_redirect http://$proxy_host:8000/ /;\n</code></pre>\n\n<p>The directive can be specified (1.1.11) using regular expressions.\nIn this case, <em><code>redirect</code></em> should either start with\nthe “<code>~</code>” symbol for a case-sensitive matching,\nor with the “<code>~*</code>” symbols for case-insensitive\nmatching.\nThe regular expression can contain named and positional captures,\nand <em><code>replacement</code></em> can reference them:</p>\n\n<pre><code>proxy_redirect ~^(http://[^:]+):\\d+(/.+)$ $1$2;\nproxy_redirect ~*/user/([^/]+)/(.+)$      http://$1.example.com/$2;\n</code></pre>\n\n<p>Several <code>proxy_redirect</code> directives\ncan be specified on the same level:</p>\n\n<pre><code>proxy_redirect default;\nproxy_redirect http://localhost:8000/  /;\nproxy_redirect http://www.example.com/ /;\n</code></pre>\n\n<p>If several directives can be applied to\nthe header fields of a proxied server response,\nthe first matching directive will be chosen.</p>\n\n<p>The <code>off</code> parameter cancels the effect\nof the <code>proxy_redirect</code> directives\ninherited from the previous configuration level.</p>\n\n<p>Using this directive, it is also possible to add host names to relative\nredirects issued by a proxied server:</p>\n\n<pre><code>proxy_redirect / /;\n</code></pre>\n"
