				isBlock: false,
				description_md: "Enables or disables buffering of a client request body.\n\nWhen buffering is enabled, the entire request body is\n[read](https://nginx.org/en/docs/http/ngx_http_core_module.html#client_body_buffer_size)\nfrom the client before sending the request to a proxied server.\n\nWhen buffering is disabled, the request body is sent to the proxied server\nimmediately as it is received.\nIn this case, the request cannot be passed to the\n[next server](https://nginx.org/en/docs/http/ngx_http_proxy_module.html#proxy_next_upstream)\nif nginx already started sending the request body.\n\nWhen HTTP/1.1 chunked transfer encoding is used\nto send the original request body,\nthe request body will be buffered regardless of the directive value unless\nHTTP/1.1 is [enabled](https://nginx.org/en/docs/http/ngx_http_proxy_module.html#proxy_http_version) for proxying.",
				description_html: "<p>Enables or disables buffering of a client request body.</p>\n\n<p>When buffering is enabled, the entire request body is\n<a href=\"https://nginx.org/en/docs/http/ngx_http_core_module.html#client_body_buffer_size\" target=\"_blank\">read</a>\nfrom the client before sending the request to a proxied server.</p>\n\n<p>When buffering is disabled, the request body is sent to the proxied server\nimmediately as it is received.\nIn this case, the request cannot be passed to the\n<a href=\"https://nginx.org/en/docs/http/ngx_http_proxy_module.html#proxy_next_upstream\" target=\"_blank\">next server</a>\nif nginx already started sending the request body.</p>\n\n<p>When HTTP/1.1 chunked transfer encoding is used\nto send the original request body,\nthe request body will be buffered regardless of the directive value unless\nHTTP/1.1 is <a href=\"https://nginx.org/en/docs/http/ngx_http_proxy_module.html#proxy_http_version\" target=\"_blank\">enabled</a> for proxying.</p>\n"
