				isBlock: true,
				description_md: "The specified *`condition`* is evaluated.\nIf true, this module directives specified inside the braces are\nexecuted, and the request is assigned the configuration inside the\n`if` directive.\nConfigurations inside the `if` directives are\ninherited from the previous configuration level.\n\nA condition may be any of the following:\n- a variable name; false if the value of a variable is an empty string\n    or “`0`”;\n    > Before version 1.0.1, any string starting with “`0`”\n    > was considered a false value.\n- comparison of a variable with a string using the\n    “`=`” and “`!=`” operators;\n- matching of a variable against a regular expression using the\n    “`~`” (for case-sensitive matching) and\n    “`~*`” (for case-insensitive matching) operators.\n    Regular expressions can contain captures that are made available for\n    later reuse in the `$1`..`$9` variables.\n    Negative operators “`!~`” and “`!~*`”\n    are also available.\n    If a regular expression includes the “`}`”\n    or “`;`” characters, the whole expressions should be enclosed\n    in single or double quotes.\n- checking of a file existence with the “`-f`” and\n    “`!-f`” operators;\n- checking of a directory existence with the “`-d`” and\n    “`!-d`” operators;\n- checking of a file, directory, or symbolic link existence with the\n    “`-e`” and “`!-e`” operators;\n- checking for an executable file with the “`-x`”\n    and “`!-x`” operators.\n\nExamples:\n```\nif ($http_user_agent ~ MSIE) {\n    rewrite ^(.*)$ /msie/$1 break;\n}\n\nif ($http_cookie ~* \"id=([^;]+)(?:;|$)\") {\n    set $id $1;\n}\n\nif ($request_method = POST) {\n    return 405;\n}\n\nif ($slow) {\n    limit_rate 10k;\n}\n\nif ($invalid_referer) {\n    return 403;\n}\n```\n> A value of the `$invalid_referer` embedded variable is set by the\n> [`valid_referers`](https://nginx.org/en/docs/http/ngx_http_referer_module.html#valid_referers) directive.",
				description_html: "<p>The specified <em><code>condition</code></em> is evaluated.\nIf true, this module directives specified inside the braces are\nexecuted, and the request is assigned the configuration inside the\n<code>if</code> directive.\nConfigurations inside the <code>if</code> directives are\ninherited from the previous configuration level.</p>\n\n<p>A condition may be any of the following:</p>\n\n<ul>\n<li>a variable name; false if the value of a variable is an empty string\nor “<code>0</code>”;\n&gt; Before version 1.0.1, any string starting with “<code>0</code>”\n&gt; was considered a false value.</li>\n<li>comparison of a variable with a string using the\n“<code>=</code>” and “<code>!=</code>” operators;</li>\n<li>matching of a variable against a regular expression using the\n“<code>~</code>” (for case-sensitive matching) and\n“<code>~*</code>” (for case-insensitive matching) operators.\nRegular expressions can contain captures that are made available for\nlater reuse in the <code>$1</code>..<code>$9</code> variables.\nNegative operators “<code>!~</code>” and “<code>!~*</code>”\nare also available.\nIf a regular expression includes the “<code>}</code>”\nor “<code>;</code>” characters, the whole expressions should be enclosed\nin single or double quotes.</li>\n<li>checking of a file existence with the “<code>-f</code>” and\n“<code>!-f</code>” operators;</li>\n<li>checking of a directory existence with the “<code>-d</code>” and\n“<code>!-d</code>” operators;</li>\n<li>checking of a file, directory, or symbolic link existence with the\n“<code>-e</code>” and “<code>!-e</code>” operators;</li>\n<li>checking for an executable file with the “<code>-x</code>”\nand “<code>!-x</code>” operators.</li>\n</ul>\n\n<p>Examples:</p>\n\n<pre><code>if ($http_user_agent ~ MSIE) {\n    rewrite ^(.*)$ /msie/$1 break;\n}\n\nif ($http_cookie ~* &quot;id=([^;]+)(?:;|$)&quot;) {\n    set $id $1;\n}\n\nif ($request_method = POST) {\n    return 405;\n}\n\nif ($slow) {\n    limit_rate 10k;\n}\n\nif ($invalid_referer) {\n    return 403;\n}\n</code></pre>\n\n<blockquote>\n<p>A value of the <code>$invalid_referer</code> embedded variable is set by the\n<a href=\"https://nginx.org/en/docs/http/ngx_http_referer_module.html#valid_referers\" target=\"_blank\"><code>valid_referers</code></a> directive.</p>\n</blockquote>\n"
