				isBlock: false,
				description_md: "Stops processing and returns the specified *`code`* to a client.\nThe non-standard code 444 closes a connection without sending\na response header.\n\nStarting from version 0.8.42, it is possible to specify\neither a redirect URL (for codes 301, 302, 303, 307, and 308)\nor the response body *`text`* (for other codes).\nA response body text and redirect URL can contain variables.\nAs a special case, a redirect URL can be specified as a URI\nlocal to this server, in which case the full redirect URL\nis formed according to the request scheme (`$scheme`) and the\n[`server_name_in_redirect`](https://nginx.org/en/docs/http/ngx_http_core_module.html#server_name_in_redirect) and\n[`port_in_redirect`](https://nginx.org/en/docs/http/ngx_http_core_module.html#port_in_redirect) directives.\n\nIn addition, a *`URL`* for temporary redirect with the code 302\ncan be specified as the sole parameter.\nSuch a parameter should start with the “`http://`”,\n“`https://`”, or “`$scheme`” string.\nA *`URL`* can contain variables.\n\n> Only the following codes could be returned before version 0.7.51:\n> 204, 400, 402 — 406, 408, 410, 411, 413, 416, and 500 — 504.\n\n\n> The code 307 was not treated as a redirect until versions 1.1.16 and 1.0.13.\n\n\n> The code 308 was not treated as a redirect until version 1.13.0.\n\nSee also the [`error_page`](https://nginx.org/en/docs/http/ngx_http_core_module.html#error_page) directive.",
				description_html: "<p>Stops processing and returns the specified <em><code>code</code></em> to a client.\nThe non-standard code 444 closes a connection without sending\na response header.</p>\n\n<p>Starting from version 0.8.42, it is possible to specify\neither a redirect URL (for codes 301, 302, 303, 307, and 308)\nor the response body <em><code>text</code></em> (for other codes).\nA response body text and redirect URL can contain variables.\nAs a special case, a redirect URL can be specified as a URI\nlocal to this server, in which case the full redirect URL\nis formed according to the request scheme (<code>$scheme</code>) and the\n<a href=\"https://nginx.org/en/docs/http/ngx_http_core_module.html#server_name_in_redirect\" target=\"_blank\"><code>server_name_in_redirect</code></a> and\n<a href=\"https://nginx.org/en/docs/http/ngx_http_core_module.html#port_in_redirect\" target=\"_blank\"><code>port_in_redirect</code></a> directives.</p>\n\n<p>In addition, a <em><code>URL</code></em> for temporary redirect with the code 302\ncan be specified as the sole parameter.\nSuch a parameter should start with the “<code>http://</code>”,\n“<code>https://</code>”, or “<code>$scheme</code>” string.\nA <em><code>URL</code></em> can contain variables.</p>\n\n<blockquote>\n<p>Only the following codes could be returned before version 0.7.51:\n204, 400, 402 — 406, 408, 410, 411, 413, 416, and 500 — 504.</p>\n\n<p>The code 307 was not treated as a redirect until versions 1.1.16 and 1.0.13.</p>\n\n<p>The code 308 was not treated as a redirect until version 1.13.0.</p>\n</blockquote>\n\n<p>See also the <a href=\"https://nginx.org/en/docs/http/ngx_http_core_module.html#error_page\" target=\"_blank\"><code>error_page</code></a> directive.</p>\n"
