				isBlock: false,
				description_md: "If the specified regular expression matches a request URI, URI is changed\nas specified in the *`replacement`* string.\nThe `rewrite` directives are executed sequentially\nin order of their appearance in the configuration file.\nIt is possible to terminate further processing of the directives using flags.\nIf a replacement string starts with “`http://`”,\n“`https://`”, or “`$scheme`”,\nthe processing stops and the redirect is returned to a client.\n\nAn optional *`flag`* parameter can be one of:\n- `last`\n\n    stops processing the current set of\n    `ngx_http_rewrite_module` directives and starts\n    a search for a new location matching the changed URI;\n- `break`\n\n    stops processing the current set of\n    `ngx_http_rewrite_module` directives\n    as with the [`break`](https://nginx.org/en/docs/http/ngx_http_rewrite_module.html#break) directive;\n- `redirect`\n\n    returns a temporary redirect with the 302 code;\n    used if a replacement string does not start with\n    “`http://`”, “`https://`”,\n    or “`$scheme`”;\n- `permanent`\n\n    returns a permanent redirect with the 301 code.\n\nThe full redirect URL is formed according to the\nrequest scheme (`$scheme`) and the\n[`server_name_in_redirect`](https://nginx.org/en/docs/http/ngx_http_core_module.html#server_name_in_redirect) and\n[`port_in_redirect`](https://nginx.org/en/docs/http/ngx_http_core_module.html#port_in_redirect) directives.\n\nExample:\n```\nserver {\n    ...\n    rewrite ^(/download/.*)/media/(.*)\\..*$ $1/mp3/$2.mp3 last;\n    rewrite ^(/download/.*)/audio/(.*)\\..*$ $1/mp3/$2.ra  last;\n    return  403;\n    ...\n}\n```\n\nBut if these directives are put inside the “`/download/`”\nlocation, the `last` flag should be replaced by\n`break`, or otherwise nginx will make 10 cycles and\nreturn the 500 error:\n```\nlocation /download/ {\n    rewrite ^(/download/.*)/media/(.*)\\..*$ $1/mp3/$2.mp3 break;\n    rewrite ^(/download/.*)/audio/(.*)\\..*$ $1/mp3/$2.ra  break;\n    return  403;\n}\n```\n\nIf a *`replacement`* string includes the new request arguments,\nthe previous request arguments are appended after them.\nIf this is undesired, putting a question mark at the end of a replacement\nstring avoids having them appended, for example:\n```\nrewrite ^/users/(.*)$ /show?user=$1? last;\n```\n\nIf a regular expression includes the “`}`”\nor “`;`” characters, the whole expressions should be enclosed\nin single or double quotes.",
				description_html: "<p>If the specified regular expression matches a request URI, URI is changed\nas specified in the <em><code>replacement</code></em> string.\nThe <code>rewrite</code> directives are executed sequentially\nin order of their appearance in the configuration file.\nIt is possible to terminate further processing of the directives using flags.\nIf a replacement string starts with “<code>http://</code>”,\n“<code>https://</code>”, or “<code>$scheme</code>”,\nthe processing stops and the redirect is returned to a client.</p>\n\n<p>An optional <em><code>flag</code></em> parameter can be one of:</p>\n\n<ul>\n<li><p><code>last</code></p>\n\n<p>stops processing the current set of\n<code>ngx_http_rewrite_module</code> directives and starts\na search for a new location matching the changed URI;</p></li>\n\n<li><p><code>break</code></p>\n\n<p>stops processing the current set of\n<code>ngx_http_rewrite_module</code> directives\nas with the <a href=\"https://nginx.org/en/docs/http/ngx_http_rewrite_module.html#break\" target=\"_blank\"><code>break</code></a> directive;</p></li>\n\n<li><p><code>redirect</code></p>\n\n<p>returns a temporary redirect with the 302 code;\nused if a replacement string does not start with\n“<code>http://</code>”, “<code>https://</code>”,\nor “<code>$scheme</code>”;</p></li>\n\n<li><p><code>permanent</code></p>\n\n<p>returns a permanent redirect with the 301 code.</p></li>\n</ul>\n\n<p>The full redirect URL is formed according to the\nrequest scheme (<code>$scheme</code>) and the\n<a href=\"https://nginx.org/en/docs/http/ngx_http_core_module.html#server_name_in_redirect\" target=\"_blank\"><code>server_name_in_redirect</code></a> and\n<a href=\"https://nginx.org/en/docs/http/ngx_http_core_module.html#port_in_redirect\" target=\"_blank\"><code>port_in_redirect</code></a> directives.</p>\n\n<p>Example:</p>\n\n<pre><code>server {\n    ...\n    rewrite ^(/download/.*)/media/(.*)\\..*$ $1/mp3/$2.mp3 last;\n    rewrite ^(/download/.*)/audio/(.*)\\..*$ $1/mp3/$2.ra  last;\n    return  403;\n    ...\n}\n</code></pre>\n\n<p>But if these directives are put inside the “<code>/download/</code>”\nlocation, the <code>last</code> flag should be replaced by\n<code>break</code>, or otherwise nginx will make 10 cycles and\nreturn the 500 error:</p>\n\n<pre><code>location /download/ {\n    rewrite ^(/download/.*)/media/(.*)\\..*$ $1/mp3/$2.mp3 break;\n    rewrite ^(/download/.*)/audio/(.*)\\..*$ $1/mp3/$2.ra  break;\n    return  403;\n}\n</code></pre>\n\n<p>If a <em><code>replacement</code></em> string includes the new request arguments,\nthe previous request arguments are appended after them.\nIf this is undesired, putting a question mark at the end of a replacement\nstring avoids having them appended, for example:</p>\n\n<pre><code>rewrite ^/users/(.*)$ /show?user=$1? last;\n</code></pre>\n\n<p>If a regular expression includes the “<code>}</code>”\nor “<code>;</code>” characters, the whole expressions should be enclosed\nin single or double quotes.</p>\n"
