				syntax_html: [
					"<p>[<em><code>code</code></em> &hellip;] <em><code>time</code></em></p>\n"
				],
				isBlock: false,
				description_md: "Sets caching time for different response codes.\nFor example, the following directives\n```\nscgi_cache_valid 200 302 10m;\nscgi_cache_valid 404      1m;\n```\nset 10 minutes of caching for responses with codes 200 and 302\nand 1 minute for responses with code 404.\n\nIf only caching *`time`* is specified\n```\nscgi_cache_valid 5m;\n```\nthen only 200, 301, and 302 responses are cached.\n\nIn addition, the `any` parameter can be specified\nto cache any responses:\n```\nscgi_cache_valid 200 302 10m;\nscgi_cache_valid 301      1h;\nscgi_cache_valid any      1m;\n```\n\nParameters of caching can also be set directly\nin the response header.\nThis has higher priority than setting of caching time using the directive.\n- The \"X-Accel-Expires\" header field sets caching time of a\n    response in seconds.\n    The zero value disables caching for a response.\n    If the value starts with the `@` prefix, it sets an absolute\n    time in seconds since Epoch, up to which the response may be cached.\n- If the header does not include the \"X-Accel-Expires\" field,\n    parameters of caching may be set in the header fields\n    \"Expires\" or \"Cache-Control\".\n- If the header includes the \"Set-Cookie\" field, such a\n    response will not be cached.\n- If the header includes the \"Vary\" field\n    with the special value “`*`”, such a\n    response will not be cached (1.7.7).\n    If the header includes the \"Vary\" field\n    with another value, such a response will be cached\n    taking into account the corresponding request header fields (1.7.7).\n\nProcessing of one or more of these response header fields can be disabled\nusing the [`scgi_ignore_headers`](https://nginx.org/en/docs/http/ngx_http_scgi_module.html#scgi_ignore_headers) directive.",
				description_html: "<p>Sets caching time for different response codes.\nFor example, the following directives</p>\n\n<pre><code>scgi_cache_valid 200 302 10m;\nscgi_cache_valid 404      1m;\n</code></pre>\n\n<p>set 10 minutes of caching for responses with codes 200 and 302\nand 1 minute for responses with code 404.</p>\n\n<p>If only caching <em><code>time</code></em> is specified</p>\n\n<pre><code>scgi_cache_valid 5m;\n</code></pre>\n\n<p>then only 200, 301, and 302 responses are cached.</p>\n\n<p>In addition, the <code>any</code> parameter can be specified\nto cache any responses:</p>\n\n<pre><code>scgi_cache_valid 200 302 10m;\nscgi_cache_valid 301      1h;\nscgi_cache_valid any      1m;\n</code></pre>\n\n<p>Parameters of caching can also be set directly\nin the response header.\nThis has higher priority than setting of caching time using the directive.</p>\n\n<ul>\n<li>The &ldquo;X-Accel-Expires&rdquo; header field sets caching time of a\nresponse in seconds.\nThe zero value disables caching for a response.\nIf the value starts with the <code>@</code> prefix, it sets an absolute\ntime in seconds since Epoch, up to which the response may be cached.</li>\n<li>If the header does not include the &ldquo;X-Accel-Expires&rdquo; field,\nparameters of caching may be set in the header fields\n&ldquo;Expires&rdquo; or &ldquo;Cache-Control&rdquo;.</li>\n<li>If the header includes the &ldquo;Set-Cookie&rdquo; field, such a\nresponse will not be cached.</li>\n<li>If the header includes the &ldquo;Vary&rdquo; field\nwith the special value “<code>*</code>”, such a\nresponse will not be cached (1.7.7).\nIf the header includes the &ldquo;Vary&rdquo; field\nwith another value, such a response will be cached\ntaking into account the corresponding request header fields (1.7.7).</li>\n</ul>\n\n<p>Processing of one or more of these response header fields can be disabled\nusing the <a href=\"https://nginx.org/en/docs/http/ngx_http_scgi_module.html#scgi_ignore_headers\" target=\"_blank\"><code>scgi_ignore_headers</code></a> directive.</p>\n"
