				description_html: "<p>When <a href=\"https://nginx.org/en/docs/http/ngx_http_scgi_module.html#scgi_buffering\" target=\"_blank\">buffering</a> of responses from the SCGI\nserver is enabled, and the whole response does not fit into the buffers\nset by the <a href=\"https://nginx.org/en/docs/http/ngx_http_scgi_module.html#scgi_buffer_size\" target=\"_blank\"><code>scgi_buffer_size</code></a> and <a href=\"https://nginx.org/en/docs/http/ngx_http_scgi_module.html#scgi_buffers\" target=\"_blank\"><code>scgi_buffers</code></a>\ndirectives, a part of the response can be saved to a temporary file.\nThis directive sets the maximum <em><code>size</code></em> of the temporary file.\nThe size of data written to the temporary file at a time is set\nby the <a href=\"https://nginx.org/en/docs/http/ngx_http_scgi_module.html#scgi_temp_file_write_size\" target=\"_blank\"><code>scgi_temp_file_write_size</code></a> directive.</p>\n\n<p>The zero value disables buffering of responses to temporary files.</p>\n\n<blockquote>\n<p>This restriction does not apply to responses\nthat will be <a href=\"https://nginx.org/en/docs/http/ngx_http_scgi_module.html#scgi_cache\" target=\"_blank\">cached</a>\nor <a href=\"https://nginx.org/en/docs/http/ngx_http_scgi_module.html#scgi_store\" target=\"_blank\">stored</a> on disk.</p>\n</blockquote>\n"
