				isBlock: false,
				description_md: "Enables saving of files to a disk.\nThe `on` parameter saves files with paths\ncorresponding to the directives\n[`alias`](https://nginx.org/en/docs/http/ngx_http_core_module.html#alias) or\n[`root`](https://nginx.org/en/docs/http/ngx_http_core_module.html#root).\nThe `off` parameter disables saving of files.\nIn addition, the file name can be set explicitly using the\n*`string`* with variables:\n```\nscgi_store /data/www$original_uri;\n```\n\nThe modification time of files is set according to the received\n\"Last-Modified\" response header field.\nThe response is first written to a temporary file,\nand then the file is renamed.\nStarting from version 0.8.9, temporary files and the persistent store\ncan be put on different file systems.\nHowever, be aware that in this case a file is copied\nacross two file systems instead of the cheap renaming operation.\nIt is thus recommended that for any given location both saved files and a\ndirectory holding temporary files, set by the [`scgi_temp_path`](https://nginx.org/en/docs/http/ngx_http_scgi_module.html#scgi_temp_path)\ndirective, are put on the same file system.\n\nThis directive can be used to create local copies of static unchangeable\nfiles, e.g.:\n```\nlocation /images/ {\n    root              /data/www;\n    error_page        404 = /fetch$uri;\n}\n\nlocation /fetch/ {\n    internal;\n\n    scgi_pass         backend:9000;\n    ...\n\n    scgi_store        on;\n    scgi_store_access user:rw group:rw all:r;\n    scgi_temp_path    /data/temp;\n\n    alias             /data/www/;\n}\n```",
				description_html: "<p>Enables saving of files to a disk.\nThe <code>on</code> parameter saves files with paths\ncorresponding to the directives\n<a href=\"https://nginx.org/en/docs/http/ngx_http_core_module.html#alias\" target=\"_blank\"><code>alias</code></a> or\n<a href=\"https://nginx.org/en/docs/http/ngx_http_core_module.html#root\" target=\"_blank\"><code>root</code></a>.\nThe <code>off</code> parameter disables saving of files.\nIn addition, the file name can be set explicitly using the\n<em><code>string</code></em> with variables:</p>\n\n<pre><code>scgi_store /data/www$original_uri;\n</code></pre>\n\n<p>The modification time of files is set according to the received\n&ldquo;Last-Modified&rdquo; response header field.\nThe response is first written to a temporary file,\nand then the file is renamed.\nStarting from version 0.8.9, temporary files and the persistent store\ncan be put on different file systems.\nHowever, be aware that in this case a file is copied\nacross two file systems instead of the cheap renaming operation.\nIt is thus recommended that for any given location both saved files and a\ndirectory holding temporary files, set by the <a href=\"https://nginx.org/en/docs/http/ngx_http_scgi_module.html#scgi_temp_path\" target=\"_blank\"><code>scgi_temp_path</code></a>\ndirective, are put on the same file system.</p>\n\n<p>This directive can be used to create local copies of static unchangeable\nfiles, e.g.:</p>\n\n<pre><code>location /images/ {\n    root              /data/www;\n    error_page        404 = /fetch$uri;\n}\n\nlocation /fetch/ {\n    internal;\n\n    scgi_pass         backend:9000;\n    ...\n\n    scgi_store        on;\n    scgi_store_access user:rw group:rw all:r;\n    scgi_temp_path    /data/temp;\n\n    alias             /data/www/;\n}\n</code></pre>\n"
