				description_html: "<p>Determines in which cases a stale cached response can be used\nwhen an error occurs during communication with the uwsgi server.\nThe directive’s parameters match the parameters of the\n<a href=\"https://nginx.org/en/docs/http/ngx_http_uwsgi_module.html#uwsgi_next_upstream\" target=\"_blank\"><code>uwsgi_next_upstream</code></a> directive.</p>\n\n<p>The <code>error</code> parameter also permits\nusing a stale cached response if a uwsgi server to process a request\ncannot be selected.</p>\n\n<p>Additionally, the <code>updating</code> parameter permits\nusing a stale cached response if it is currently being updated.\nThis allows minimizing the number of accesses to uwsgi servers\nwhen updating cached data.</p>\n\n<p>Using a stale cached response\ncan also be enabled directly in the response header\nfor a specified number of seconds after the response became stale (1.11.10).\nThis has lower priority than using the directive parameters.</p>\n\n<ul>\n<li>The\n“<a href=\"https://datatracker.ietf.org/doc/html/rfc5861#section-3\" target=\"_blank\">stale-while-revalidate</a>”\nextension of the &ldquo;Cache-Control&rdquo; header field permits\nusing a stale cached response if it is currently being updated.</li>\n<li>The\n“<a href=\"https://datatracker.ietf.org/doc/html/rfc5861#section-4\" target=\"_blank\">stale-if-error</a>”\nextension of the &ldquo;Cache-Control&rdquo; header field permits\nusing a stale cached response in case of an error.</li>\n</ul>\n\n<p>To minimize the number of accesses to uwsgi servers when\npopulating a new cache element, the <a href=\"https://nginx.org/en/docs/http/ngx_http_uwsgi_module.html#uwsgi_cache_lock\" target=\"_blank\"><code>uwsgi_cache_lock</code></a>\ndirective can be used.</p>\n"
