				isBlock: false,
				description_md: "Disables processing of certain response header fields from the uwsgi server.\nThe following fields can be ignored: \"X-Accel-Redirect\",\n\"X-Accel-Expires\", \"X-Accel-Limit-Rate\" (1.1.6),\n\"X-Accel-Buffering\" (1.1.6),\n\"X-Accel-Charset\" (1.1.6), \"Expires\",\n\"Cache-Control\", \"Set-Cookie\" (0.8.44),\nand \"Vary\" (1.7.7).\n\nIf not disabled, processing of these header fields has the following\neffect:\n- \"X-Accel-Expires\", \"Expires\",\n    \"Cache-Control\", \"Set-Cookie\",\n    and \"Vary\"\n    set the parameters of response [caching](https://nginx.org/en/docs/http/ngx_http_uwsgi_module.html#uwsgi_cache_valid);\n- \"X-Accel-Redirect\" performs an\n    [internal redirect](https://nginx.org/en/docs/http/ngx_http_core_module.html#internal) to the specified URI;\n- \"X-Accel-Limit-Rate\" sets the\n    [rate limit](https://nginx.org/en/docs/http/ngx_http_core_module.html#limit_rate) for transmission of a response to a client;\n- \"X-Accel-Buffering\" enables or disables\n    [buffering](https://nginx.org/en/docs/http/ngx_http_uwsgi_module.html#uwsgi_buffering) of a response;\n- \"X-Accel-Charset\" sets the desired\n    [`charset`](https://nginx.org/en/docs/http/ngx_http_charset_module.html#charset)\n    of a response.",
				description_html: "<p>Disables processing of certain response header fields from the uwsgi server.\nThe following fields can be ignored: &ldquo;X-Accel-Redirect&rdquo;,\n&ldquo;X-Accel-Expires&rdquo;, &ldquo;X-Accel-Limit-Rate&rdquo; (1.1.6),\n&ldquo;X-Accel-Buffering&rdquo; (1.1.6),\n&ldquo;X-Accel-Charset&rdquo; (1.1.6), &ldquo;Expires&rdquo;,\n&ldquo;Cache-Control&rdquo;, &ldquo;Set-Cookie&rdquo; (0.8.44),\nand &ldquo;Vary&rdquo; (1.7.7).</p>\n\n<p>If not disabled, processing of these header fields has the following\neffect:</p>\n\n<ul>\n<li>&ldquo;X-Accel-Expires&rdquo;, &ldquo;Expires&rdquo;,\n&ldquo;Cache-Control&rdquo;, &ldquo;Set-Cookie&rdquo;,\nand &ldquo;Vary&rdquo;\nset the parameters of response <a href=\"https://nginx.org/en/docs/http/ngx_http_uwsgi_module.html#uwsgi_cache_valid\" target=\"_blank\">caching</a>;</li>\n<li>&ldquo;X-Accel-Redirect&rdquo; performs an\n<a href=\"https://nginx.org/en/docs/http/ngx_http_core_module.html#internal\" target=\"_blank\">internal redirect</a> to the specified URI;</li>\n<li>&ldquo;X-Accel-Limit-Rate&rdquo; sets the\n<a href=\"https://nginx.org/en/docs/http/ngx_http_core_module.html#limit_rate\" target=\"_blank\">rate limit</a> for transmission of a response to a client;</li>\n<li>&ldquo;X-Accel-Buffering&rdquo; enables or disables\n<a href=\"https://nginx.org/en/docs/http/ngx_http_uwsgi_module.html#uwsgi_buffering\" target=\"_blank\">buffering</a> of a response;</li>\n<li>&ldquo;X-Accel-Charset&rdquo; sets the desired\n<a href=\"https://nginx.org/en/docs/http/ngx_http_charset_module.html#charset\" target=\"_blank\"><code>charset</code></a>\nof a response.</li>\n</ul>\n"
