				description_md: "Sets the protocol and address of a uwsgi server.\nAs a *`protocol`*,\n“`uwsgi`” or “`suwsgi`”\n(secured uwsgi, uwsgi over SSL) can be specified.\nThe address can be specified as a domain name or IP address,\nand a port:\n```\nuwsgi_pass localhost:9000;\nuwsgi_pass uwsgi://localhost:9000;\nuwsgi_pass suwsgi://[2001:db8::1]:9090;\n```\nor as a UNIX-domain socket path:\n```\nuwsgi_pass unix:/tmp/uwsgi.socket;\n```\n\nIf a domain name resolves to several addresses, all of them will be\nused in a round-robin fashion.\nIn addition, an address can be specified as a\n[server group](https://nginx.org/en/docs/http/ngx_http_upstream_module.html).\n\nParameter value can contain variables.\nIn this case, if an address is specified as a domain name,\nthe name is searched among the described\n[server groups](https://nginx.org/en/docs/http/ngx_http_upstream_module.html),\nand, if not found, is determined using a\n[`resolver`](https://nginx.org/en/docs/http/ngx_http_core_module.html#resolver).\n\n> Secured uwsgi protocol is supported since version 1.5.8.",
				description_html: "<p>Sets the protocol and address of a uwsgi server.\nAs a <em><code>protocol</code></em>,\n“<code>uwsgi</code>” or “<code>suwsgi</code>”\n(secured uwsgi, uwsgi over SSL) can be specified.\nThe address can be specified as a domain name or IP address,\nand a port:</p>\n\n<pre><code>uwsgi_pass localhost:9000;\nuwsgi_pass uwsgi://localhost:9000;\nuwsgi_pass suwsgi://[2001:db8::1]:9090;\n</code></pre>\n\n<p>or as a UNIX-domain socket path:</p>\n\n<pre><code>uwsgi_pass unix:/tmp/uwsgi.socket;\n</code></pre>\n\n<p>If a domain name resolves to several addresses, all of them will be\nused in a round-robin fashion.\nIn addition, an address can be specified as a\n<a href=\"https://nginx.org/en/docs/http/ngx_http_upstream_module.html\" target=\"_blank\">server group</a>.</p>\n\n<p>Parameter value can contain variables.\nIn this case, if an address is specified as a domain name,\nthe name is searched among the described\n<a href=\"https://nginx.org/en/docs/http/ngx_http_upstream_module.html\" target=\"_blank\">server groups</a>,\nand, if not found, is determined using a\n<a href=\"https://nginx.org/en/docs/http/ngx_http_core_module.html#resolver\" target=\"_blank\"><code>resolver</code></a>.</p>\n\n<blockquote>\n<p>Secured uwsgi protocol is supported since version 1.5.8.</p>\n</blockquote>\n"
