				description_html: "<blockquote>\n<p>This directive is obsolete since version 1.19.7.\nThe <a href=\"https://nginx.org/en/docs/http/ngx_http_core_module.html#large_client_header_buffers\" target=\"_blank\"><code>large_client_header_buffers</code></a>\ndirective should be used instead.</p>\n</blockquote>\n\n<p>Limits the maximum size of\nan <a href=\"https://datatracker.ietf.org/doc/html/rfc7541\" target=\"_blank\">HPACK</a>-compressed\nrequest header field.\nThe limit applies equally to both name and value.\nNote that if Huffman encoding is applied,\nthe actual size of decompressed name and value strings may be larger.\nFor most requests, the default limit should be enough.</p>\n"
