				description_md: "Defines the string parameters for XSLT stylesheets.\nXPath expressions in the *`value`* are not interpreted.\nThe *`value`* can contain variables.\n\nThere could be several `xslt_string_param` directives.\nThese directives are inherited from the previous configuration level\nif and only if there are no [`xslt_param`](https://nginx.org/en/docs/http/ngx_http_xslt_module.html#xslt_param) and\n`xslt_string_param` directives\ndefined on the current level.",
				description_html: "<p>Defines the string parameters for XSLT stylesheets.\nXPath expressions in the <em><code>value</code></em> are not interpreted.\nThe <em><code>value</code></em> can contain variables.</p>\n\n<p>There could be several <code>xslt_string_param</code> directives.\nThese directives are inherited from the previous configuration level\nif and only if there are no <a href=\"https://nginx.org/en/docs/http/ngx_http_xslt_module.html#xslt_param\" target=\"_blank\"><code>xslt_param</code></a> and\n<code>xslt_string_param</code> directives\ndefined on the current level.</p>\n"
