				isBlock: false,
				description_md: "Sets the protocol for a proxied server.\nSupported protocols are\n[IMAP](https://nginx.org/en/docs/mail/ngx_mail_imap_module.html),\n[POP3](https://nginx.org/en/docs/mail/ngx_mail_pop3_module.html), and\n[SMTP](https://nginx.org/en/docs/mail/ngx_mail_smtp_module.html).\n\nIf the directive is not set, the protocol can be detected automatically\nbased on the well-known port specified in the [`listen`](https://nginx.org/en/docs/mail/ngx_mail_core_module.html#listen)\ndirective:\n- `imap`: 143, 993\n- `pop3`: 110, 995\n- `smtp`: 25, 587, 465\n\nUnnecessary protocols can be disabled using the\n[configuration](https://nginx.org/en/docs/configure.html)\nparameters `--without-mail_imap_module`,\n`--without-mail_pop3_module`, and\n`--without-mail_smtp_module`.",
				description_html: "<p>Sets the protocol for a proxied server.\nSupported protocols are\n<a href=\"https://nginx.org/en/docs/mail/ngx_mail_imap_module.html\" target=\"_blank\">IMAP</a>,\n<a href=\"https://nginx.org/en/docs/mail/ngx_mail_pop3_module.html\" target=\"_blank\">POP3</a>, and\n<a href=\"https://nginx.org/en/docs/mail/ngx_mail_smtp_module.html\" target=\"_blank\">SMTP</a>.</p>\n\n<p>If the directive is not set, the protocol can be detected automatically\nbased on the well-known port specified in the <a href=\"https://nginx.org/en/docs/mail/ngx_mail_core_module.html#listen\" target=\"_blank\"><code>listen</code></a>\ndirective:</p>\n\n<ul>\n<li><code>imap</code>: 143, 993</li>\n<li><code>pop3</code>: 110, 995</li>\n<li><code>smtp</code>: 25, 587, 465</li>\n</ul>\n\n<p>Unnecessary protocols can be disabled using the\n<a href=\"https://nginx.org/en/docs/configure.html\" target=\"_blank\">configuration</a>\nparameters <code>--without-mail_imap_module</code>,\n<code>--without-mail_pop3_module</code>, and\n<code>--without-mail_smtp_module</code>.</p>\n"
