				description_md: "Sets a *`file`* with the secret key used to encrypt\nand decrypt TLS session tickets.\nThe directive is necessary if the same key has to be shared between\nmultiple servers.\nBy default, a randomly generated key is used.\n\nIf several keys are specified, only the first key is\nused to encrypt TLS session tickets.\nThis allows configuring key rotation, for example:\n```\nssl_session_ticket_key current.key;\nssl_session_ticket_key previous.key;\n```\n\nThe *`file`* must contain 80 or 48 bytes\nof random data and can be created using the following command:\n```\nopenssl rand 80 > ticket.key\n```\nDepending on the file size either AES256 (for 80-byte keys, 1.11.8)\nor AES128 (for 48-byte keys) is used for encryption.",
				description_html: "<p>Sets a <em><code>file</code></em> with the secret key used to encrypt\nand decrypt TLS session tickets.\nThe directive is necessary if the same key has to be shared between\nmultiple servers.\nBy default, a randomly generated key is used.</p>\n\n<p>If several keys are specified, only the first key is\nused to encrypt TLS session tickets.\nThis allows configuring key rotation, for example:</p>\n\n<pre><code>ssl_session_ticket_key current.key;\nssl_session_ticket_key previous.key;\n</code></pre>\n\n<p>The <em><code>file</code></em> must contain 80 or 48 bytes\nof random data and can be created using the following command:</p>\n\n<pre><code>openssl rand 80 &gt; ticket.key\n</code></pre>\n\n<p>Depending on the file size either AES256 (for 80-byte keys, 1.11.8)\nor AES128 (for 48-byte keys) is used for encryption.</p>\n"
