				isBlock: false,
				description_md: "By default, nginx removes all environment variables inherited\nfrom its parent process except the TZ variable.\nThis directive allows preserving some of the inherited variables,\nchanging their values, or creating new environment variables.\nThese variables are then:\n- inherited during a [live upgrade](https://nginx.org/en/docs/control.html#upgrade)\n    of an executable file;\n- used by the\n    [ngx_http_perl_module](https://nginx.org/en/docs/http/ngx_http_perl_module.html) module;\n- used by worker processes.\n    One should bear in mind that controlling system libraries in this way\n    is not always possible as it is common for libraries to check\n    variables only during initialization, well before they can be set\n    using this directive.\n    An exception from this is an above mentioned\n    [live upgrade](https://nginx.org/en/docs/control.html#upgrade)\n    of an executable file.\n\nThe TZ variable is always inherited and available to the\n[ngx_http_perl_module](https://nginx.org/en/docs/http/ngx_http_perl_module.html)\nmodule, unless it is configured explicitly.\n\nUsage example:\n```\nenv MALLOC_OPTIONS;\nenv PERL5LIB=/data/site/modules;\nenv OPENSSL_ALLOW_PROXY_CERTS=1;\n```\n\n> The NGINX environment variable is used internally by nginx\n> and should not be set directly by the user.",
				description_html: "<p>By default, nginx removes all environment variables inherited\nfrom its parent process except the TZ variable.\nThis directive allows preserving some of the inherited variables,\nchanging their values, or creating new environment variables.\nThese variables are then:</p>\n\n<ul>\n<li>inherited during a <a href=\"https://nginx.org/en/docs/control.html#upgrade\" target=\"_blank\">live upgrade</a>\nof an executable file;</li>\n<li>used by the\n<a href=\"https://nginx.org/en/docs/http/ngx_http_perl_module.html\" target=\"_blank\">ngx_http_perl_module</a> module;</li>\n<li>used by worker processes.\nOne should bear in mind that controlling system libraries in this way\nis not always possible as it is common for libraries to check\nvariables only during initialization, well before they can be set\nusing this directive.\nAn exception from this is an above mentioned\n<a href=\"https://nginx.org/en/docs/control.html#upgrade\" target=\"_blank\">live upgrade</a>\nof an executable file.</li>\n</ul>\n\n<p>The TZ variable is always inherited and available to the\n<a href=\"https://nginx.org/en/docs/http/ngx_http_perl_module.html\" target=\"_blank\">ngx_http_perl_module</a>\nmodule, unless it is configured explicitly.</p>\n\n<p>Usage example:</p>\n\n<pre><code>env MALLOC_OPTIONS;\nenv PERL5LIB=/data/site/modules;\nenv OPENSSL_ALLOW_PROXY_CERTS=1;\n</code></pre>\n\n<blockquote>\n<p>The NGINX environment variable is used internally by nginx\nand should not be set directly by the user.</p>\n</blockquote>\n"
