				isBlock: false,
				description_md: "Configures logging.\nSeveral logs can be specified on the same configuration level (1.5.2).\nIf on the `main` configuration level writing a log to a file\nis not explicitly defined, the default file will be used.\n\nThe first parameter defines a *`file`* that will store the log.\n\nThe special value `stderr` selects the standard error file.\nLogging to [syslog](https://nginx.org/en/docs/syslog.html) can be configured by specifying\nthe “`syslog:`” prefix.\nLogging to a\n[cyclic memory buffer](https://nginx.org/en/docs/debugging_log.html#memory)\ncan be configured by specifying the “`memory:`” prefix and\nbuffer *`size`*, and is generally used for debugging (1.7.11).\n\nThe second parameter determines the *`level`* of logging,\nand can be one of the following:\n`debug`, `info`, `notice`,\n`warn`, `error`, `crit`,\n`alert`, or `emerg`.\nLog levels above are listed in the order of increasing severity.\nSetting a certain log level will cause all messages of\nthe specified and more severe log levels to be logged.\nFor example, the default level `error` will\ncause `error`, `crit`,\n`alert`, and `emerg` messages\nto be logged.\nIf this parameter is omitted then `error` is used.\n> For `debug` logging to work, nginx needs to\n> be built with `--with-debug`,\n> see “[A debugging log](https://nginx.org/en/docs/debugging_log.html)”.\n\n\n> The directive can be specified on the\n> `stream` level\n> starting from version 1.7.11,\n> and on the `mail` level\n> starting from version 1.9.0.",
				description_html: "<p>Configures logging.\nSeveral logs can be specified on the same configuration level (1.5.2).\nIf on the <code>main</code> configuration level writing a log to a file\nis not explicitly defined, the default file will be used.</p>\n\n<p>The first parameter defines a <em><code>file</code></em> that will store the log.</p>\n\n<p>The special value <code>stderr</code> selects the standard error file.\nLogging to <a href=\"https://nginx.org/en/docs/syslog.html\" target=\"_blank\">syslog</a> can be configured by specifying\nthe “<code>syslog:</code>” prefix.\nLogging to a\n<a href=\"https://nginx.org/en/docs/debugging_log.html#memory\" target=\"_blank\">cyclic memory buffer</a>\ncan be configured by specifying the “<code>memory:</code>” prefix and\nbuffer <em><code>size</code></em>, and is generally used for debugging (1.7.11).</p>\n\n<p>The second parameter determines the <em><code>level</code></em> of logging,\nand can be one of the following:\n<code>debug</code>, <code>info</code>, <code>notice</code>,\n<code>warn</code>, <code>error</code>, <code>crit</code>,\n<code>alert</code>, or <code>emerg</code>.\nLog levels above are listed in the order of increasing severity.\nSetting a certain log level will cause all messages of\nthe specified and more severe log levels to be logged.\nFor example, the default level <code>error</code> will\ncause <code>error</code>, <code>crit</code>,\n<code>alert</code>, and <code>emerg</code> messages\nto be logged.\nIf this parameter is omitted then <code>error</code> is used.</p>\n\n<blockquote>\n<p>For <code>debug</code> logging to work, nginx needs to\nbe built with <code>--with-debug</code>,\nsee “<a href=\"https://nginx.org/en/docs/debugging_log.html\" target=\"_blank\">A debugging log</a>”.</p>\n\n<p>The directive can be specified on the\n<code>stream</code> level\nstarting from version 1.7.11,\nand on the <code>mail</code> level\nstarting from version 1.9.0.</p>\n</blockquote>\n"
