				syntax_html: [
					"<p><em><code>address</code></em> &hellip; [<code>valid</code>=<em><code>time</code></em>] [<code>ipv4</code>=<code>on</code>|<code>off</code>] [<code>ipv6</code>=<code>on</code>|<code>off</code>] [<code>status_zone</code>=<em><code>zone</code></em>]</p>\n"
				],
				isBlock: false,
				description_md: "Configures name servers used to resolve names of upstream servers\ninto addresses, for example:\n```\nresolver 127.0.0.1 [::1]:5353;\n```\nThe address can be specified as a domain name or IP address,\nwith an optional port.\nIf port is not specified, the port 53 is used.\nName servers are queried in a round-robin fashion.\n\nBy default, nginx will look up both IPv4 and IPv6 addresses while resolving.\nIf looking up of IPv4 or IPv6 addresses is not desired,\nthe `ipv4=off` (1.23.1) or\nthe `ipv6=off` parameter can be specified.\n\nBy default, nginx caches answers using the TTL value of a response.\nThe optional `valid` parameter allows overriding it:\n```\nresolver 127.0.0.1 [::1]:5353 valid=30s;\n```\n> To prevent DNS spoofing, it is recommended\n> configuring DNS servers in a properly secured trusted local network.\n\nThe optional `status_zone` parameter (1.17.1)\nenables\n[collection](https://nginx.org/en/docs/http/ngx_http_api_module.html#resolvers_)\nof DNS server statistics of requests and responses\nin the specified *`zone`*.\nThe parameter is available as part of our\n[commercial subscription](https://nginx.com/products/).\n\n> Before version 1.11.3, this directive was available as part of our\n> [commercial subscription](https://nginx.com/products/).",
				description_html: "<p>Configures name servers used to resolve names of upstream servers\ninto addresses, for example:</p>\n\n<pre><code>resolver 127.0.0.1 [::1]:5353;\n</code></pre>\n\n<p>The address can be specified as a domain name or IP address,\nwith an optional port.\nIf port is not specified, the port 53 is used.\nName servers are queried in a round-robin fashion.</p>\n\n<p>By default, nginx will look up both IPv4 and IPv6 addresses while resolving.\nIf looking up of IPv4 or IPv6 addresses is not desired,\nthe <code>ipv4=off</code> (1.23.1) or\nthe <code>ipv6=off</code> parameter can be specified.</p>\n\n<p>By default, nginx caches answers using the TTL value of a response.\nThe optional <code>valid</code> parameter allows overriding it:</p>\n\n<pre><code>resolver 127.0.0.1 [::1]:5353 valid=30s;\n</code></pre>\n\n<blockquote>\n<p>To prevent DNS spoofing, it is recommended\nconfiguring DNS servers in a properly secured trusted local network.</p>\n</blockquote>\n\n<p>The optional <code>status_zone</code> parameter (1.17.1)\nenables\n<a href=\"https://nginx.org/en/docs/http/ngx_http_api_module.html#resolvers_\" target=\"_blank\">collection</a>\nof DNS server statistics of requests and responses\nin the specified <em><code>zone</code></em>.\nThe parameter is available as part of our\n<a href=\"https://nginx.com/products/\" target=\"_blank\">commercial subscription</a>.</p>\n\n<blockquote>\n<p>Before version 1.11.3, this directive was available as part of our\n<a href=\"https://nginx.com/products/\" target=\"_blank\">commercial subscription</a>.</p>\n</blockquote>\n"
