				isBlock: true,
				description_md: "Sets the configuration for a virtual server.\nThere is no clear separation between IP-based (based on the IP address)\nand name-based (based on the\n[TLS Server Name Indication extension](http://en.wikipedia.org/wiki/Server_Name_Indication) (SNI, RFC 6066)) (1.25.5)\nvirtual servers.\nInstead, the [`listen`](https://nginx.org/en/docs/stream/ngx_stream_core_module.html#listen) directives describe all\naddresses and ports that should accept connections for the server, and the\n[`server_name`](https://nginx.org/en/docs/stream/ngx_stream_core_module.html#server_name) directive lists all server names.",
				description_html: "<p>Sets the configuration for a virtual server.\nThere is no clear separation between IP-based (based on the IP address)\nand name-based (based on the\n<a href=\"http://en.wikipedia.org/wiki/Server_Name_Indication\" target=\"_blank\">TLS Server Name Indication extension</a> (SNI, RFC 6066)) (1.25.5)\nvirtual servers.\nInstead, the <a href=\"https://nginx.org/en/docs/stream/ngx_stream_core_module.html#listen\" target=\"_blank\"><code>listen</code></a> directives describe all\naddresses and ports that should accept connections for the server, and the\n<a href=\"https://nginx.org/en/docs/stream/ngx_stream_core_module.html#server_name\" target=\"_blank\"><code>server_name</code></a> directive lists all server names.</p>\n"
