				description_html: "<p>Sets an njs function which will be called at the\n<a href=\"https://nginx.org/en/docs/stream/stream_processing.html#access_phase\" target=\"_blank\">access</a> phase.\nSince <a href=\"https://nginx.org/en/docs/njs/changes.html#njs0.4.0\" target=\"_blank\">0.4.0</a>,\na module function can be referenced.</p>\n\n<p>The function is called once at the moment when the stream session reaches\nthe <a href=\"https://nginx.org/en/docs/stream/stream_processing.html#access_phase\" target=\"_blank\">access</a> phase\nfor the first time.\nThe function is called with the following arguments:</p>\n\n<ul>\n<li><p><code>s</code></p>\n\n<p>the <a href=\"https://nginx.org/en/docs/njs/reference.html#stream\" target=\"_blank\">Stream Session</a> object</p></li>\n</ul>\n\n<p>At this phase, it is possible to perform initialization\nor register a callback with\nthe <a href=\"https://nginx.org/en/docs/njs/reference.html#s_on\" target=\"_blank\"><code>s.on()</code></a>\nmethod\nfor each incoming data chunk until one of the following methods are called:\n<a href=\"https://nginx.org/en/docs/njs/reference.html#s_allow\" target=\"_blank\"><code>s.allow()</code></a>,\n<a href=\"https://nginx.org/en/docs/njs/reference.html#s_decline\" target=\"_blank\"><code>s.decline()</code></a>,\n<a href=\"https://nginx.org/en/docs/njs/reference.html#s_done\" target=\"_blank\"><code>s.done()</code></a>.\nAs soon as one of these methods is called, the stream session processing\nswitches to the <a href=\"https://nginx.org/en/docs/stream/stream_processing.html\" target=\"_blank\">next phase</a>\nand all current\n<a href=\"https://nginx.org/en/docs/njs/reference.html#s_on\" target=\"_blank\"><code>s.on()</code></a>\ncallbacks are dropped.</p>\n"
