				description_md: "Sets a data filter.\nSince [0.4.0](https://nginx.org/en/docs/njs/changes.html#njs0.4.0),\na module function can be referenced.\nThe filter function is called once at the moment when the stream session reaches\nthe [content](https://nginx.org/en/docs/stream/stream_processing.html#content_phase) phase.\n\nThe filter function is called with the following arguments:\n- `s`\n\n    the [Stream Session](https://nginx.org/en/docs/njs/reference.html#stream) object\n\nAt this phase, it is possible to perform initialization\nor register a callback with\nthe [`s.on()`](https://nginx.org/en/docs/njs/reference.html#s_on)\nmethod for each incoming data chunk.\nThe\n[`s.off()`](https://nginx.org/en/docs/njs/reference.html#s_off)\nmethod may be used to unregister a callback and stop filtering.\n\n> As the `js_filter` handler\n> returns its result immediately, it supports\n> only synchronous operations.\n> Thus, asynchronous operations such as\n> [`ngx.fetch()`](https://nginx.org/en/docs/njs/reference.html#ngx_fetch)\n> or\n> [`setTimeout()`](https://nginx.org/en/docs/njs/reference.html#settimeout)\n> are not supported.",
				description_html: "<p>Sets a data filter.\nSince <a href=\"https://nginx.org/en/docs/njs/changes.html#njs0.4.0\" target=\"_blank\">0.4.0</a>,\na module function can be referenced.\nThe filter function is called once at the moment when the stream session reaches\nthe <a href=\"https://nginx.org/en/docs/stream/stream_processing.html#content_phase\" target=\"_blank\">content</a> phase.</p>\n\n<p>The filter function is called with the following arguments:</p>\n\n<ul>\n<li><p><code>s</code></p>\n\n<p>the <a href=\"https://nginx.org/en/docs/njs/reference.html#stream\" target=\"_blank\">Stream Session</a> object</p></li>\n</ul>\n\n<p>At this phase, it is possible to perform initialization\nor register a callback with\nthe <a href=\"https://nginx.org/en/docs/njs/reference.html#s_on\" target=\"_blank\"><code>s.on()</code></a>\nmethod for each incoming data chunk.\nThe\n<a href=\"https://nginx.org/en/docs/njs/reference.html#s_off\" target=\"_blank\"><code>s.off()</code></a>\nmethod may be used to unregister a callback and stop filtering.</p>\n\n<blockquote>\n<p>As the <code>js_filter</code> handler\nreturns its result immediately, it supports\nonly synchronous operations.\nThus, asynchronous operations such as\n<a href=\"https://nginx.org/en/docs/njs/reference.html#ngx_fetch\" target=\"_blank\"><code>ngx.fetch()</code></a>\nor\n<a href=\"https://nginx.org/en/docs/njs/reference.html#settimeout\" target=\"_blank\"><code>setTimeout()</code></a>\nare not supported.</p>\n</blockquote>\n"
