				name: "js_periodic",
				"default": "",
				contexts: [
					"server"
				],
				syntax_md: [
					"*`function`* | *`module.function`* [`interval`=*`time`*] [`jitter`=*`number`*] [`worker_affinity`=*`mask`*]"
				],
				syntax_html: [
					"<p><em><code>function</code></em> | <em><code>module.function</code></em> [<code>interval</code>=<em><code>time</code></em>] [<code>jitter</code>=<em><code>number</code></em>] [<code>worker_affinity</code>=<em><code>mask</code></em>]</p>\n"
				],
				isBlock: false,
				description_md: "Specifies a content handler to run at regular interval.\nThe handler receives a\n[session object](https://nginx.org/en/docs/njs/reference.html#periodic_session)\nas its first argument,\nit also has access to global objects such as\n[ngx](https://nginx.org/en/docs/njs/reference.html#ngx).\n\nThe optional `interval` parameter\nsets the interval between two consecutive runs,\nby default, 5 seconds.\n\nThe optional `jitter` parameter sets the time within which\nthe location content handler will be randomly delayed,\nby default, there is no delay.\n\nBy default, the `js_handler` is executed on worker process 0.\nThe optional `worker_affinity` parameter\nallows specifying particular worker processes\nwhere the location content handler should be executed.\nEach worker process set is represented by a bitmask of allowed worker processes.\nThe `all` mask allows the handler to be executed\nin all worker processes.\n\nExample:\n```\nexample.conf:\n\nlocation @periodics {\n    # to be run at 1 minute intervals in worker process 0\n    js_periodic main.handler interval=60s;\n\n    # to be run at 1 minute intervals in all worker processes\n    js_periodic main.handler interval=60s worker_affinity=all;\n\n    # to be run at 1 minute intervals in worker processes 1 and 3\n    js_periodic main.handler interval=60s worker_affinity=0101;\n\n    resolver 10.0.0.1;\n    js_fetch_trusted_certificate /path/to/ISRG_Root_X1.pem;\n}\n\nexample.js:\n\nasync function handler(s) {\n    let reply = await ngx.fetch('https://nginx.org/en/docs/njs/');\n    let body = await reply.text();\n\n    ngx.log(ngx.INFO, body);\n}\n```",
				description_html: "<p>Specifies a content handler to run at regular interval.\nThe handler receives a\n<a href=\"https://nginx.org/en/docs/njs/reference.html#periodic_session\" target=\"_blank\">session object</a>\nas its first argument,\nit also has access to global objects such as\n<a href=\"https://nginx.org/en/docs/njs/reference.html#ngx\" target=\"_blank\">ngx</a>.</p>\n\n<p>The optional <code>interval</code> parameter\nsets the interval between two consecutive runs,\nby default, 5 seconds.</p>\n\n<p>The optional <code>jitter</code> parameter sets the time within which\nthe location content handler will be randomly delayed,\nby default, there is no delay.</p>\n\n<p>By default, the <code>js_handler</code> is executed on worker process 0.\nThe optional <code>worker_affinity</code> parameter\nallows specifying particular worker processes\nwhere the location content handler should be executed.\nEach worker process set is represented by a bitmask of allowed worker processes.\nThe <code>all</code> mask allows the handler to be executed\nin all worker processes.</p>\n\n<p>Example:</p>\n\n<pre><code>example.conf:\n\nlocation @periodics {\n    # to be run at 1 minute intervals in worker process 0\n    js_periodic main.handler interval=60s;\n\n    # to be run at 1 minute intervals in all worker processes\n    js_periodic main.handler interval=60s worker_affinity=all;\n\n    # to be run at 1 minute intervals in worker processes 1 and 3\n    js_periodic main.handler interval=60s worker_affinity=0101;\n\n    resolver 10.0.0.1;\n    js_fetch_trusted_certificate /path/to/ISRG_Root_X1.pem;\n}\n\nexample.js:\n\nasync function handler(s) {\n    let reply = await ngx.fetch('https://nginx.org/en/docs/njs/');\n    let body = await reply.text();\n\n    ngx.log(ngx.INFO, body);\n}\n</code></pre>\n"
