				isBlock: false,
				description_md: "Sets the types and sizes of caches that store session parameters.\nA cache can be of any of the following types:\n- `off`\n\n    the use of a session cache is strictly prohibited:\n    nginx explicitly tells a client that sessions may not be reused.\n- `none`\n\n    the use of a session cache is gently disallowed:\n    nginx tells a client that sessions may be reused, but does not\n    actually store session parameters in the cache.\n- `builtin`\n\n    a cache built in OpenSSL; used by one worker process only.\n    The cache size is specified in sessions.\n    If size is not given, it is equal to 20480 sessions.\n    Use of the built-in cache can cause memory fragmentation.\n- `shared`\n\n    a cache shared between all worker processes.\n    The cache size is specified in bytes; one megabyte can store\n    about 4000 sessions.\n    Each shared cache should have an arbitrary name.\n    A cache with the same name can be used in several\n    servers.\n    It is also used to automatically generate, store, and\n    periodically rotate TLS session ticket keys (1.23.2)\n    unless configured explicitly\n    using the [`ssl_session_ticket_key`](https://nginx.org/en/docs/stream/ngx_stream_ssl_module.html#ssl_session_ticket_key) directive.\n\nBoth cache types can be used simultaneously, for example:\n```\nssl_session_cache builtin:1000 shared:SSL:10m;\n```\nbut using only shared cache without the built-in cache should\nbe more efficient.",
				description_html: "<p>Sets the types and sizes of caches that store session parameters.\nA cache can be of any of the following types:</p>\n\n<ul>\n<li><p><code>off</code></p>\n\n<p>the use of a session cache is strictly prohibited:\nnginx explicitly tells a client that sessions may not be reused.</p></li>\n\n<li><p><code>none</code></p>\n\n<p>the use of a session cache is gently disallowed:\nnginx tells a client that sessions may be reused, but does not\nactually store session parameters in the cache.</p></li>\n\n<li><p><code>builtin</code></p>\n\n<p>a cache built in OpenSSL; used by one worker process only.\nThe cache size is specified in sessions.\nIf size is not given, it is equal to 20480 sessions.\nUse of the built-in cache can cause memory fragmentation.</p></li>\n\n<li><p><code>shared</code></p>\n\n<p>a cache shared between all worker processes.\nThe cache size is specified in bytes; one megabyte can store\nabout 4000 sessions.\nEach shared cache should have an arbitrary name.\nA cache with the same name can be used in several\nservers.\nIt is also used to automatically generate, store, and\nperiodically rotate TLS session ticket keys (1.23.2)\nunless configured explicitly\nusing the <a href=\"https://nginx.org/en/docs/stream/ngx_stream_ssl_module.html#ssl_session_ticket_key\" target=\"_blank\"><code>ssl_session_ticket_key</code></a> directive.</p></li>\n</ul>\n\n<p>Both cache types can be used simultaneously, for example:</p>\n\n<pre><code>ssl_session_cache builtin:1000 shared:SSL:10m;\n</code></pre>\n\n<p>but using only shared cache without the built-in cache should\nbe more efficient.</p>\n"
