				isBlock: false,
				description_md: "Overrides the\n[`proxy_timeout`](https://nginx.org/en/docs/stream/ngx_stream_proxy_module.html#proxy_timeout)\nvalue for health checks.",
				description_html: "<p>Overrides the\n<a href=\"https://nginx.org/en/docs/stream/ngx_stream_proxy_module.html#proxy_timeout\" target=\"_blank\"><code>proxy_timeout</code></a>\nvalue for health checks.</p>\n"
