				isBlock: false,
				description_md: "Defines the *`name`* and *`size`* of the shared\nmemory zone that keeps the group’s configuration and run-time state that are\nshared between worker processes.\nSeveral groups may share the same zone.\nIn this case, it is enough to specify the *`size`* only once.\n\nAdditionally,\nas part of our [commercial subscription](https://nginx.com/products/),\nsuch groups allow changing the group membership\nor modifying the settings of a particular server\nwithout the need of restarting nginx.\nThe configuration is accessible via the\n[API](https://nginx.org/en/docs/http/ngx_http_api_module.html) module (1.13.3).\n> Prior to version 1.13.3,\n> the configuration was accessible only via a special location\n> handled by\n> [`upstream_conf`](https://nginx.org/en/docs/http/ngx_http_upstream_conf_module.html#upstream_conf).",
				description_html: "<p>Defines the <em><code>name</code></em> and <em><code>size</code></em> of the shared\nmemory zone that keeps the group’s configuration and run-time state that are\nshared between worker processes.\nSeveral groups may share the same zone.\nIn this case, it is enough to specify the <em><code>size</code></em> only once.</p>\n\n<p>Additionally,\nas part of our <a href=\"https://nginx.com/products/\" target=\"_blank\">commercial subscription</a>,\nsuch groups allow changing the group membership\nor modifying the settings of a particular server\nwithout the need of restarting nginx.\nThe configuration is accessible via the\n<a href=\"https://nginx.org/en/docs/http/ngx_http_api_module.html\" target=\"_blank\">API</a> module (1.13.3).</p>\n\n<blockquote>\n<p>Prior to version 1.13.3,\nthe configuration was accessible only via a special location\nhandled by\n<a href=\"https://nginx.org/en/docs/http/ngx_http_upstream_conf_module.html#upstream_conf\" target=\"_blank\"><code>upstream_conf</code></a>.</p>\n</blockquote>\n"
