				isBlock: false,
				description_md: "Specifies that a group should use a load balancing method where a connection\nis passed to a randomly selected server, taking into account weights\nof servers.\n\nThe optional `two` parameter\ninstructs nginx to randomly select\n[two](https://homes.cs.washington.edu/~karlin/papers/balls.pdf)\nservers and then choose a server\nusing the specified `method`.\nThe default method is `least_conn`\nwhich passes a connection to a server\nwith the least number of active connections.\n\nThe `least_time` method passes a connection to a server\nwith the least average time and least number of active connections.\nIf `least_time=connect` parameter is specified,\ntime to\n[connect](https://nginx.org/en/docs/stream/ngx_stream_upstream_module.html#var_upstream_connect_time)\nto the upstream server is used.\nIf `least_time=first_byte` parameter is specified,\ntime to receive the\n[first byte](https://nginx.org/en/docs/stream/ngx_stream_upstream_module.html#var_upstream_first_byte_time) of data is used.\nIf `least_time=last_byte` is specified,\ntime to receive the\n[last byte](https://nginx.org/en/docs/stream/ngx_stream_upstream_module.html#var_upstream_session_time) of data is used.\n> The `least_time` method is available as a part of our\n> [commercial subscription](https://nginx.com/products/).",
				description_html: "<p>Specifies that a group should use a load balancing method where a connection\nis passed to a randomly selected server, taking into account weights\nof servers.</p>\n\n<p>The optional <code>two</code> parameter\ninstructs nginx to randomly select\n<a href=\"https://homes.cs.washington.edu/~karlin/papers/balls.pdf\" target=\"_blank\">two</a>\nservers and then choose a server\nusing the specified <code>method</code>.\nThe default method is <code>least_conn</code>\nwhich passes a connection to a server\nwith the least number of active connections.</p>\n\n<p>The <code>least_time</code> method passes a connection to a server\nwith the least average time and least number of active connections.\nIf <code>least_time=connect</code> parameter is specified,\ntime to\n<a href=\"https://nginx.org/en/docs/stream/ngx_stream_upstream_module.html#var_upstream_connect_time\" target=\"_blank\">connect</a>\nto the upstream server is used.\nIf <code>least_time=first_byte</code> parameter is specified,\ntime to receive the\n<a href=\"https://nginx.org/en/docs/stream/ngx_stream_upstream_module.html#var_upstream_first_byte_time\" target=\"_blank\">first byte</a> of data is used.\nIf <code>least_time=last_byte</code> is specified,\ntime to receive the\n<a href=\"https://nginx.org/en/docs/stream/ngx_stream_upstream_module.html#var_upstream_session_time\" target=\"_blank\">last byte</a> of data is used.</p>\n\n<blockquote>\n<p>The <code>least_time</code> method is available as a part of our\n<a href=\"https://nginx.com/products/\" target=\"_blank\">commercial subscription</a>.</p>\n</blockquote>\n"
